// MessageDetail.js - 统一使用全局搜索进行高亮和定位
import React, { useState, useRef, useEffect, useCallback } from 'react';
import { Code, FileText, Terminal, BookOpen} from 'lucide-react';
import ReactMarkdown from 'react-markdown';
import { Prism as SyntaxHighlighter } from 'react-syntax-highlighter';
import { vscDarkPlus } from 'react-syntax-highlighter/dist/esm/styles/prism';
import remarkGfm from 'remark-gfm';

// --- Helper Functions ---

/**
 * Wraps matching text nodes with spans for highlighting.
 * @param {Node} node - The DOM node to process.
 * @param {string} query - The search query (now global).
 * @param {Array<HTMLElement>} highlights - An array to store references to the created highlight spans.
 */
const wrapTextNodes = (node, query, highlights) => {
  try {
    // Check if node is still connected to the DOM
    if (!node || !node.isConnected) {
      return;
    }

    if (node.nodeType === Node.TEXT_NODE) {
      const text = node.textContent;
      if (!text || !query || query.trim() === '') {
        return;
      }

      const lowerText = text.toLowerCase();
      const lowerQuery = query.toLowerCase();
      let lastIndex = 0;
      let index = lowerText.indexOf(lowerQuery);
      
      // Skip processing if no matches
      if (index === -1) {
        return;
      }
      
      const fragment = document.createDocumentFragment();

      while (index !== -1) {
        if (index > lastIndex) {
          fragment.appendChild(document.createTextNode(text.substring(lastIndex, index)));
        }
        const span = document.createElement('span');
        span.textContent = text.substring(index, index + lowerQuery.length);
        // Apply a consistent class for all matches based on the global query
        span.className = 'global-search-highlight';
        fragment.appendChild(span);
        highlights.push(span); // Store reference if needed elsewhere, though less critical now

        lastIndex = index + lowerQuery.length;
        index = lowerText.indexOf(lowerQuery, lastIndex);
      }

      if (lastIndex < text.length) {
        fragment.appendChild(document.createTextNode(text.substring(lastIndex)));
      }

      // Check if we've created a meaningful fragment with at least one highlight
      if (fragment.childNodes.length > 0 && !(fragment.childNodes.length === 1 && fragment.firstChild.nodeType === Node.TEXT_NODE)) {
        // Ensure both the node and its parent are still in the DOM
        if (node.parentNode && node.isConnected && node.parentNode.isConnected) {
          try {
            node.parentNode.replaceChild(fragment, node);
          } catch (replaceError) {
            console.error('Error replacing node during highlight:', replaceError);
          }
        }
      }
    } else if (node.nodeType === Node.ELEMENT_NODE) {
      // Skip nodes that shouldn't be processed or that might cause issues
      if (node.tagName !== 'SCRIPT' && 
          node.tagName !== 'STYLE' && 
          node.tagName !== 'NOSCRIPT' &&
          !node.closest('pre, code')) {
        
        // Create a static copy of childNodes to prevent issues with live NodeList during DOM changes
        const childNodes = Array.from(node.childNodes);
        childNodes.forEach(child => wrapTextNodes(child, query, highlights));
      }
    }
  } catch (error) {
    console.error('Error in wrapTextNodes:', error);
  }
};

/**
 * Removes previously added global highlight spans.
 * @param {HTMLElement} container - The container element.
 */
const removeHighlights = (container) => {
  if (!container) return;
  try {
    // Find spans with the global highlight class
    const highlights = container.querySelectorAll('span.global-search-highlight');
    highlights.forEach(span => {
      const parent = span.parentNode;
      if (parent) {
        // Create the text node before attempting to replace
        const textNode = document.createTextNode(span.textContent || '');
        // Only replace if span is still in the DOM
        if (span.isConnected) {
          parent.replaceChild(textNode, span);
          parent.normalize();
        }
      }
    });
  } catch (error) {
    console.error('Error removing highlights:', error);
  }
  // No need to remove '.current-match' as it's no longer used here
};


// --- Sub-Components (Simplified for brevity, focus on highlighting integration) ---

// Content view component
const ContentView = ({ message, globalSearchQuery }) => {
  const contentRef = useRef(null);

  // Effect to apply/remove highlights based on global query
  useEffect(() => {
    let isMounted = true;
    let timer;
    
    // Wrap in setTimeout to ensure rendering is complete
    timer = setTimeout(() => {
      // Check if component is still mounted
      if (!isMounted) return;
      
      try {
        const container = contentRef.current;
        if (!container) return;
        
        // Always remove previous highlights first
        removeHighlights(container);
        
        // Only apply new highlights if there's a query
        if (globalSearchQuery && globalSearchQuery.trim()) {
          const highlights = [];
          wrapTextNodes(container, globalSearchQuery, highlights);
        }
      } catch (error) {
        console.error('Error handling highlights in ContentView:', error);
      }
    }, 50); // Short delay to ensure DOM is ready
    
    // Cleanup function
    return () => {
      isMounted = false;
      clearTimeout(timer);
      
      // Try to clean up highlights on unmount to prevent orphaned nodes
      try {
        const container = contentRef.current;
        if (container) {
          removeHighlights(container);
        }
      } catch (unmountError) {
        console.error('Error cleaning up highlights on unmount:', unmountError);
      }
    };
  }, [globalSearchQuery, message.display_text]); // Explicit dependency on display_text, not the entire message

  return (
    <div ref={contentRef} className="markdown-content">
      {/* Render main message text */}
      <ReactMarkdown 
        remarkPlugins={[remarkGfm]} 
        components={{ /* Custom renderers... */ }}
        key={`md-${message.index}`} // Add a key to force re-render when message changes
      >
        {message.display_text}
      </ReactMarkdown>
    </div>
  );
};

// Thinking process view component
