import React, { useState } from 'react';
import { ChevronDown, ChevronRight, Copy } from 'lucide-react';

// 分支统计面板组件 - 包含UUID和详细分支信息
const BranchStatsPanel = ({ processedData, selectedMessageIndices = [] }) => {
  const [expanded, setExpanded] = useState(true);
  const [showFullUuids, setShowFullUuids] = useState(false);
  const [copiedText, setCopiedText] = useState('');
  
  // 检查是否有可用的分支数据
  const hasBranches = processedData && Array.isArray(processedData.branches) && processedData.branches.length > 0;
  
  // 从选中的消息获取分支信息，如果没有选中消息则从meta_info获取
  let messageData = null;
  if (processedData?.chat_history && selectedMessageIndices.length > 0) {
    // 使用选中的第一条消息
    const selectedIndex = selectedMessageIndices[0];
    messageData = processedData.chat_history.find(msg => msg.index === selectedIndex);
  }
  
  // 如果有选中的消息，使用其数据；否则使用默认meta_info
  const metaInfo = messageData || processedData?.meta_info || {};
  
  // 分支ID和级别
  const branchId = messageData?.branch || metaInfo.branch_id || "1";
  const branchLevel = messageData?.branch_level || metaInfo.branch_level || "1";
  
  // UUID信息
  const messageUuid = messageData?.uuid || metaInfo.uuid || "-";
  const parentUuid = messageData?.parent_uuid || metaInfo.parent_uuid || "-";
  
  // 处理UUID显示，截断太长的UUID
  const truncateUuid = (uuid) => {
    if (!uuid || uuid === "-") return "-";
    return showFullUuids ? uuid : `${uuid.substring(0, 8)}...${uuid.substring(uuid.length - 8)}`;
  };
  
  // 复制文本到剪贴板
  const copyToClipboard = (text) => {
    if (!text || text === "-") return;
    
    navigator.clipboard.writeText(text)
      .then(() => {
        setCopiedText(text);
        setTimeout(() => setCopiedText(''), 2000);
      })
      .catch(err => console.error('复制失败:', err));
  };
  
  return (
    <div className="p-3 tab-content-container">
      <div className="bg-white p-4 rounded border border-gray-200 panel-content-area">
        <div 
          className="flex items-center justify-between cursor-pointer mb-3"
          onClick={() => setExpanded(!expanded)}
        >
          <div className="flex items-center font-medium text-gray-700">
            {expanded ? 
              <ChevronDown className="h-4 w-4 mr-1" /> : 
              <ChevronRight className="h-4 w-4 mr-1" />
            }
            <span>分支统计详情</span>
          </div>
        </div>
        
        {expanded && (
          hasBranches ? (
            <div className="grid grid-cols-2 gap-4">
              {/* 左侧：分支详细信息 */}
              <div>
                <h3 className="text-sm font-medium text-blue-600 mb-3">分支详细信息</h3>
                
                <div className="mb-2">
                  <div className="text-gray-700 mb-1 text-sm">分支ID:</div>
                  <div className="font-medium text-blue-700">{branchId}</div>
                </div>
                
                <div className="mb-2">
                  <div className="text-gray-700 mb-1 text-sm">分支级别:</div>
                  <div className="font-medium text-blue-700">{branchLevel}</div>
                </div>
                
                <div className="mb-2">
                  <div className="text-gray-700 mb-1 text-sm flex items-center">
                    消息UUID:
                    {messageUuid !== "-" && (
                      <button 
                        onClick={() => copyToClipboard(messageUuid)}
                        className="ml-1 text-gray-400 hover:text-blue-500"
                        title="复制完整UUID"
                      >
                        <Copy size={14} />
                      </button>
                    )}
                    {copiedText === messageUuid && (
                      <span className="ml-1 text-xs text-green-500">已复制!</span>
                    )}
                  </div>
                  <div className="font-medium text-blue-700 break-all text-sm">
                    {truncateUuid(messageUuid)}
                  </div>
                </div>
                
                <div className="mb-2">
                  <div className="text-gray-700 mb-1 text-sm flex items-center">
                    父消息UUID:
                    {parentUuid !== "-" && (
                      <button 
                        onClick={() => copyToClipboard(parentUuid)}
                        className="ml-1 text-gray-400 hover:text-blue-500"
                        title="复制完整UUID"
                      >
                        <Copy size={14} />
                      </button>
                    )}
                    {copiedText === parentUuid && (
                      <span className="ml-1 text-xs text-green-500">已复制!</span>
                    )}
                  </div>
                  <div className="font-medium text-blue-700 break-all text-sm">
                    {truncateUuid(parentUuid)}
                  </div>
                </div>
              </div>
              
              {/* 右侧：原有的统计信息 */}
              <div>
                <h3 className="text-sm font-medium text-blue-600 mb-3">统计数据</h3>
                
                <div className="mb-2">
                  <div className="text-gray-700 mb-1 text-sm">总分支数:</div>
                  <div className="font-medium text-blue-700">
                    {processedData.branches.length}
                  </div>
                </div>
                
                <div className="mb-2">
                  <div className="text-gray-700 mb-1 text-sm">分支点数量:</div>
                  <div className="font-medium text-blue-700">
                    {processedData.chat_history?.filter(msg => msg.is_branch_point).length || 0}
                  </div>
                </div>
                
                <div className="mb-2">
                  <div className="text-gray-700 mb-1 text-sm">最长分支:</div>
                  <div className="font-medium text-blue-700">
                    {processedData.branches.reduce((max, branch) => 
                      Math.max(max, Array.isArray(branch) ? branch.length : 0), 0)}条消息
                  </div>
                </div>
                
                <div className="mb-2">
                  <div className="text-gray-700 mb-1 text-sm">最大分支深度:</div>
                  <div className="font-medium text-blue-700">
                    {processedData.chat_history?.reduce((max, msg) => 
                      Math.max(max, msg.branch_level || 0), 0) || 0}
                  </div>
                </div>
              </div>
            </div>
          ) : (
            <div className="text-gray-500 text-center py-6 bg-gray-50 rounded border border-gray-200">
              当前对话没有分支数据
            </div>
          )
        )}
        
        {expanded && hasBranches && (
          <div className="mt-4 p-3 bg-blue-50 rounded border border-blue-200 text-sm">
            <div className="font-medium text-blue-700 mb-1">提示：</div>
            <p>您可以在左侧分支管理部分选择特定分支以查看和导出其中的消息。分支选择后，相应的消息会在消息列表中被高亮显示。</p>
          </div>
        )}
      </div>
    </div>
  );
};

export default BranchStatsPanel;