import React, { useState, useEffect, useCallback, useRef } from 'react';
import { Search, X, Loader, ChevronDown, ChevronRight, ExternalLink } from 'lucide-react';

/**
 * 增强型全局搜索面板 - 解决跨文件跳转和结果排序问题
 */
const SearchPanel = ({
  isOpen,
  onClose,
  files,
  currentFileIndex,
  processedData,
  searchQuery,
  setSearchQuery,
  jumpToMessage,
  handleSelectFile
}) => {
  const [results, setResults] = useState([]);
  const [isSearching, setIsSearching] = useState(false);
  const [statusMessage, setStatusMessage] = useState('');
  const [currentResultIndex, setCurrentResultIndex] = useState(-1);
  const [expandedFiles, setExpandedFiles] = useState({});
  const [flattenedResults, setFlattenedResults] = useState([]);
  const [onlyCurrentFile, setOnlyCurrentFile] = useState(false);
  const [errorMessage, setErrorMessage] = useState('');
  const [isNavigating, setIsNavigating] = useState(false);

  // 使用ref来跟踪组件是否仍然挂载和导航状态
  const isMountedRef = useRef(true);
  // 跟踪导航防抖计时器
  const navigationTimerRef = useRef(null);
  // 点击操作计数器 - 防止快速点击
  const clickCounterRef = useRef(0);

  // 设置组件挂载状态
  useEffect(() => {
    isMountedRef.current = true;
    return () => {
      isMountedRef.current = false;
      if (navigationTimerRef.current) {
        clearTimeout(navigationTimerRef.current);
        navigationTimerRef.current = null;
      }
    };
  }, []);

  // 安全地设置状态的函数，只有在组件仍然挂载时才更新状态
  const safeSetState = (setter, value) => {
    if (isMountedRef.current) {
      try {
        setter(value);
      } catch (error) {
        console.error("设置状态失败:", error);
      }
    }
  };

  // 监听搜索查询变化，自动执行搜索
  // 跟踪上一次搜索查询，避免重复触发相同的搜索
  const lastSearchQueryRef = useRef('');
  const lastOnlyCurrentFileRef = useRef(false);

  useEffect(() => {
    if (isOpen && searchQuery.trim()) {
      // 如果搜索查询相同且搜索范围也相同，不重新触发搜索
      const isSameSearch = 
        searchQuery.trim() === lastSearchQueryRef.current.trim() && 
        onlyCurrentFile === lastOnlyCurrentFileRef.current;
        
      // 更新引用值以便下次比较
      lastSearchQueryRef.current = searchQuery;
      lastOnlyCurrentFileRef.current = onlyCurrentFile;
      
      // 如果是相同的搜索且已有结果，则不触发新搜索
      if (isSameSearch && results.length > 0) {
        return;
      }
      
      // 清除之前的定时器
      if (navigationTimerRef.current) {
        clearTimeout(navigationTimerRef.current);
      }

      navigationTimerRef.current = setTimeout(() => {
        // 确保组件仍然挂载且搜索面板仍然打开
        if (!isMountedRef.current || !isOpen) return;
        
        // 确保搜索查询有效且搜索管理器存在
        if (searchQuery.trim() && window.searchManager) {
          safeSetState(setIsSearching, true);
          safeSetState(setStatusMessage, '正在搜索...');

          // 使用全局搜索管理器搜索
          window.searchManager.search(searchQuery);
        }
      }, 300);
    }
  }, [isOpen, searchQuery, onlyCurrentFile, results.length]);

  // 监听全局搜索状态更新
  useEffect(() => {
    const handleSearchUpdate = (e) => {
      if (!isMountedRef.current || !isOpen) return;

      const { results, activeIndex, totalCount, query } = e.detail;

      // 只处理与当前搜索查询匹配的更新
      if (query === searchQuery || (!query && !searchQuery)) {
        if (totalCount > 0) {
          // 在搜索面板中更新结果
          updatePanelWithSearchResults(results, activeIndex);
        } else {
          // 清空结果
          safeSetState(setFlattenedResults, []);
          safeSetState(setResults, []);
          safeSetState(setStatusMessage, `未找到包含 "${searchQuery}" 的消息`);
          safeSetState(setIsSearching, false);
        }
      }
    };

    window.addEventListener('search-status-update', handleSearchUpdate);
    return () => window.removeEventListener('search-status-update', handleSearchUpdate);
  }, [isOpen, searchQuery]);

  // 将搜索管理器的结果更新到面板
  const updatePanelWithSearchResults = (searchResults, activeIndex) => {
    if (!Array.isArray(searchResults) || !isMountedRef.current) return;

    safeSetState(setIsSearching, false);

    // 按文件组织结果
    const resultsByFile = {};

    // 首先将所有结果分组到对应文件
    searchResults.forEach(result => {
      if (result && typeof result.fileIndex === 'number') {
        const fileIndex = result.fileIndex;

        if (!resultsByFile[fileIndex]) {
          resultsByFile[fileIndex] = {
            fileIndex,
            fileName: files[fileIndex]?.name || 'Unknown File',
            title: (fileIndex === currentFileIndex && processedData?.meta_info?.title) ||
                  files[fileIndex]?.name || 'Unknown File',
            matchCount: 0,
            matches: []
          };
        }

        resultsByFile[fileIndex].matches.push({
          messageIndex: result.messageIndex,
          preview: result.preview || '',
          highlightedPreview: highlightTextWithQuery(result.preview || '', searchQuery)
        });

        resultsByFile[fileIndex].matchCount++;
      }
    });

    // 转换为数组并排序 - 当前文件优先
    const resultArray = Object.values(resultsByFile).sort((a, b) => {
      if (a.fileIndex === currentFileIndex) return -1;
      if (b.fileIndex === currentFileIndex) return 1;
      return 0;
    });

    safeSetState(setResults, resultArray);

    // 扁平化结果用于导航
    const flat = searchResults.map((result, idx) => ({
      fileIndex: result.fileIndex,
      messageIndex: result.messageIndex,
      fileName: files[result.fileIndex]?.name || 'Unknown File',
      preview: result.preview || ''
    }));

    safeSetState(setFlattenedResults, flat);

    // 更新当前结果索引
    if (activeIndex >= 0 && activeIndex < flat.length) {
      safeSetState(setCurrentResultIndex, activeIndex);
    } else if (flat.length > 0) {
      safeSetState(setCurrentResultIndex, 0);
    } else {
      safeSetState(setCurrentResultIndex, -1);
    }

    // 确保活动文件是展开的
    const newExpandedState = { ...expandedFiles };
    if (flat.length > 0 && activeIndex >= 0) {
      const activeFileIndex = flat[activeIndex]?.fileIndex;
      if (typeof activeFileIndex === 'number') {
        newExpandedState[activeFileIndex] = true;
      }
    }

    // 始终展开包含结果的文件
    resultArray.forEach(fileResult => {
      if (fileResult.matchCount > 0) {
        newExpandedState[fileResult.fileIndex] = true;
      }
    });

    safeSetState(setExpandedFiles, newExpandedState);

    // 更新状态消息
    const searchScope = onlyCurrentFile ? "当前文件" : `${resultArray.length} 个文件`;
    safeSetState(setStatusMessage, `在${searchScope}中找到 ${flat.length} 条匹配消息`);
  };

  // 重置组件状态
  const resetState = useCallback(() => {
    if (!isMountedRef.current) return;

    safeSetState(setResults, []);
    safeSetState(setCurrentResultIndex, -1);
    safeSetState(setStatusMessage, '');
    safeSetState(setExpandedFiles, {});
    safeSetState(setFlattenedResults, []);
    safeSetState(setErrorMessage, '');
    safeSetState(setIsNavigating, false);
  }, []);

  // 清除搜索函数
  const clearSearch = useCallback(() => {
    if (!isMountedRef.current) return;

    // 重置内部状态
    resetState();

    // 清空搜索查询
    setSearchQuery('');

    // 关闭搜索面板
    onClose();

    // 通知SearchManager清除搜索
    if (window.searchManager && typeof window.searchManager.clearSearch === 'function') {
      setTimeout(() => {
        window.searchManager.clearSearch();
      }, 100);
    }
  }, [resetState, setSearchQuery, onClose]);

  // 当面板打开/关闭时重置状态
  useEffect(() => {
    if (!isOpen) {
      resetState();
    } else if (searchQuery.trim() && window.searchManager) {
      // 获取当前搜索状态
      const searchState = window.searchManager.getState();

      if (searchState.results && searchState.results.length > 0) {
        // 已经有搜索结果，直接使用
        updatePanelWithSearchResults(searchState.results, searchState.activeIndex);
      } else if (searchQuery.trim()) {
        // 执行新搜索
        safeSetState(setIsSearching, true);
        safeSetState(setStatusMessage, '正在搜索...');
        window.searchManager.search(searchQuery);
      }
    }
  }, [isOpen, searchQuery, resetState]);

  // 高亮查询文本
  const highlightTextWithQuery = (text, query) => {
    if (!text || !query) return text;

    const lowerText = text.toLowerCase();
    const lowerQuery = query.toLowerCase();

    const parts = [];
    let lastIndex = 0;
    let index = lowerText.indexOf(lowerQuery);

    while (index !== -1) {
      // 添加查询词前面的文本
      if (index > lastIndex) {
        parts.push({
          text: text.substring(lastIndex, index),
          highlight: false
        });
      }

      // 添加高亮的查询词
      parts.push({
        text: text.substring(index, index + lowerQuery.length),
        highlight: true
      });

      lastIndex = index + lowerQuery.length;
      index = lowerText.indexOf(lowerQuery, lastIndex);
    }

    // 添加剩余文本
    if (lastIndex < text.length) {
      parts.push({
        text: text.substring(lastIndex),
        highlight: false
      });
    }

    return parts;
  };

  // 切换文件展开/折叠
  const toggleFileExpanded = (fileIndex) => {
    safeSetState(setExpandedFiles, prev => ({
      ...prev,
      [fileIndex]: !prev[fileIndex]
    }));
  };

  // 清除搜索
  const clearSearchInput = () => {
    setSearchQuery('');
    resetState();
  };

  // 切换仅搜索当前文件
  const toggleOnlyCurrentFile = () => {
    safeSetState(setOnlyCurrentFile, prev => !prev);
    // 更改选项后重新搜索
    if (window.searchManager) {
      setTimeout(() => {
        if (searchQuery.trim() && isMountedRef.current) {
          safeSetState(setIsSearching, true);
          window.searchManager.search(searchQuery);
        }
      }, 10);
    }
  };

  // 安全地关闭面板并执行回调
  const safeClose = useCallback((callback) => {
    // 如果已经在导航中，不执行任何操作
    if (isNavigating) return;

    safeSetState(setIsNavigating, true);

    // 确保当前没有导航正在进行
    if (window.searchManager && typeof window.searchManager.getState === 'function') {
      const state = window.searchManager.getState();
      if (state.activeIndex !== currentResultIndex) {
        // 同步当前结果索引
        safeSetState(setCurrentResultIndex, state.activeIndex);
      }
    }

    // 关闭搜索面板
    onClose();

    // 如果提供了回调，则在短暂延迟后执行
    if (callback && typeof callback === 'function') {
      setTimeout(() => {
        try {
          // 执行回调前再次检查组件是否已卸载
          if (isMountedRef.current) {
            callback();
          }
        } catch (error) {
          console.error("执行回调时出错:", error);
        }
      }, 100);
    }
  }, [onClose, currentResultIndex, isNavigating]);

  // 点击单个搜索结果 - 修复版本，防止DOM错误
  const handleResultClick = (fileIndex, messageIndex, allResultsIndex) => {
    // 如果已经在导航中，不执行任何操作
    if (isNavigating) return;

    // 立即设置导航状态，防止重复点击
    safeSetState(setIsNavigating, true);

    // 记录当前搜索状态用于恢复（如果需要）
    const currentSearchState = window.searchManager?.getState
      ? window.searchManager.getState()
      : null;

    try {
      // 1. 更新索引（但不立即导航）
      if (window.searchManager) {
        const updateEvent = new CustomEvent('search-index-update', {
          detail: { index: allResultsIndex }
        });
        window.dispatchEvent(updateEvent);
      }

      // 2. 关闭面板 - 最关键的一步，确保先完全卸载SearchPanel
      onClose();

      // 3. 在面板完全卸载后执行导航
      setTimeout(() => {
        // 安全检查 - 组件可能已经卸载
        if (!document.body.contains(document.activeElement)) return;

        try {
          if (window.searchManager) {
            window.searchManager.goToResult(allResultsIndex);
          } else {
            // 传统导航方式
            jumpToMessage(fileIndex, messageIndex);
          }
        } catch (error) {
          console.error("导航到搜索结果失败:", error);
        }
      }, 200); // 增加延迟确保面板已完全卸载
    } catch (error) {
      console.error("处理搜索结果点击出错:", error);
      // 恢复状态
      safeSetState(setIsNavigating, false);
      onClose(); // 确保面板关闭
    }
  };

  // 键盘导航处理
  useEffect(() => {
    const handleKeyDown = (e) => {
      if (!isOpen || isNavigating) return;

      // 如果事件是在输入框上触发的，只处理Escape键和Enter键
      if (e.target.tagName === 'INPUT' && e.key !== 'Escape' && e.key !== 'Enter') return;

      if (e.key === 'Escape') {
        e.preventDefault();
        onClose(); // 使用onClose而不是clearSearch，只关闭面板不清除搜索状态
      }
    };

    window.addEventListener('keydown', handleKeyDown);
    return () => window.removeEventListener('keydown', handleKeyDown);
  }, [isOpen, onClose, isNavigating]);

  // 如果面板未打开，不渲染任何内容
  if (!isOpen) return null;

  // 获取所有结果以便索引计算
  const allResults = flattenedResults;
  const totalResultCount = allResults.length;

  return (
    <>
      {/* 背景遮罩层 - 点击时关闭搜索面板 */}
      <div 
        className="fixed inset-0 bg-black/40 z-40" 
        onClick={() => onClose()}
        aria-hidden="true"
      />
      
      <div className="fixed top-10 left-20 right-20 bg-white rounded-lg shadow-xl border border-gray-300 z-50 max-h-[80vh] overflow-auto">
      {/* 标题栏 */}
      <div className="p-3 border-b border-gray-300 bg-blue-50 sticky top-0 z-10">
        <div className="flex justify-between items-center">
          <div className="flex-1">
            <h3 className="font-bold text-blue-800 flex items-center">
              <Search className="h-5 w-5 mr-2 text-blue-600" />
              全局搜索
            </h3>
          </div>

          {/* 搜索控件 */}
          <div className="flex-1 mx-4">
            <div className="relative">
              <input
                type="text"
                className="w-full px-10 py-2 border border-blue-300 rounded focus:ring-2 focus:ring-blue-300 focus:border-blue-500 transition-all"
                placeholder={onlyCurrentFile ? "搜索当前对话..." : "搜索所有对话..."}
                value={searchQuery}
                onChange={(e) => setSearchQuery(e.target.value)}
                autoFocus
              />
              <Search className="absolute left-3 top-2.5 h-5 w-5 text-gray-400" />

              {searchQuery && (
                <button
                  className="absolute right-3 top-2.5 text-gray-400 hover:text-gray-600 focus:outline-none"
                  onClick={clearSearchInput}
                  title="清除搜索"
                  disabled={isSearching || isNavigating}
                >
                  <X className="h-5 w-5" />
                </button>
              )}
            </div>
          </div>

          {/* 导航和关闭按钮 - 保留计数器但移除导航按钮 */}
          <div className="flex items-center space-x-2">
            {totalResultCount > 0 && (
              <div className="text-sm font-medium px-2 bg-blue-100 rounded-md py-1">
                {currentResultIndex + 1}/{totalResultCount}
              </div>
            )}

            <button
              className="ml-3 bg-gray-200 hover:bg-gray-300 px-3 py-1.5 rounded focus:outline-none focus:ring-2 focus:ring-gray-400 transition-colors disabled:opacity-50"
              onClick={onClose} // 只使用onClose关闭面板
              disabled={isNavigating}
            >
              关闭
            </button>
          </div>
        </div>

        {/* 搜索选项 */}
        <div className="mt-2 flex items-center text-sm">
          {files.length > 1 && !onlyCurrentFile && (
            <span className="ml-3 text-gray-500">
              当前搜索范围: 所有 {files.length} 个文件
            </span>
          )}
        </div>
      </div>

      {/* 搜索状态和错误消息 */}
      <div className="px-3 py-2 bg-gray-50 border-b border-gray-200 sticky top-16 z-[5]">
        <div className="flex flex-col">
          <div className="flex items-center justify-between">
            <div className="flex items-center">
              {isSearching ? (
                <div className="flex items-center text-blue-600">
                  <Loader className="animate-spin h-4 w-4 mr-2" />
                  <span>正在搜索...</span>
                </div>
              ) : (
                <div>{statusMessage}</div>
              )}
            </div>

            {totalResultCount > 0 && (
              <div className="text-sm text-gray-600">
                <span className="text-xs text-gray-500">(Esc: 结束当前搜索)</span>
              </div>
            )}
          </div>

          {/* 错误消息显示 */}
          {errorMessage && (
            <div className="mt-1 text-red-600 text-sm flex items-center">
              <span>⚠️ {errorMessage}</span>
            </div>
          )}
        </div>
      </div>

      {/* 搜索结果列表 */}
      <div className="p-3">
        {results.length === 0 ? (
          <div className="text-center py-4 text-gray-500">
            {isSearching ? (
              <div className="flex flex-col items-center">
                <Loader className="animate-spin h-8 w-8 mb-2 text-blue-500" />
                <span>正在搜索{onlyCurrentFile ? "当前对话" : "所有对话"}...</span>
              </div>
            ) : (
              searchQuery.trim() ? "没有找到匹配的结果" : "请输入搜索关键词"
            )}
          </div>
        ) : (
          <div>
            {results.map((fileResult, fileIdx) => {
              const isExpanded = expandedFiles[fileResult.fileIndex] || false;

              // 计算此文件中第一个结果的全局索引
              let globalStartIndex = 0;
              for (let i = 0; i < fileIdx; i++) {
                globalStartIndex += results[i].matches.length;
              }

              return (
                <div key={`file-${fileResult.fileIndex}`} className="mb-4 border border-gray-200 rounded-lg overflow-hidden">
                  {/* 文件标题 */}
                  <div
                    className={`p-3 flex justify-between items-center cursor-pointer ${
                      fileResult.fileIndex === currentFileIndex
                        ? 'bg-blue-100 hover:bg-blue-200'
                        : 'bg-gray-50 hover:bg-gray-100'
                    }`}
                    onClick={() => toggleFileExpanded(fileResult.fileIndex)}
                  >
                    <div className="font-medium text-blue-700 flex items-center">
                      {isExpanded ? (
                        <ChevronDown className="h-4 w-4 mr-1" />
                      ) : (
                        <ChevronRight className="h-4 w-4 mr-1" />
                      )}
                      <span>{fileResult.title || fileResult.fileName}</span>

                      {fileResult.fileIndex === currentFileIndex && (
                        <span className="ml-2 text-xs bg-blue-600 text-white px-2 py-0.5 rounded-full">
                          当前文件
                        </span>
                      )}
                    </div>

                    <div className="flex items-center">
                      <div className="text-sm bg-blue-100 text-blue-800 px-2 py-0.5 rounded-full">
                        {fileResult.matchCount}个结果
                      </div>

                      {/* 如果不是当前文件，添加"直接切换到此文件"按钮 */}
                      {fileResult.fileIndex !== currentFileIndex && (
                        <button
                          className="ml-2 p-1 text-blue-600 hover:text-blue-800 rounded disabled:opacity-50"
                          onClick={(e) => {
                            e.stopPropagation(); // 阻止事件冒泡
                            if (isNavigating) return;
                            safeClose(() => handleSelectFile(fileResult.fileIndex));
                          }}
                          title="切换到此文件"
                          disabled={isNavigating}
                        >
                          <ExternalLink size={16} />
                        </button>
                      )}
                    </div>
                  </div>

                  {/* 匹配项列表 - 仅在展开时显示 */}
                  {isExpanded && (
                    <div className="border-t border-gray-200">
                      {fileResult.matches.map((match, matchIdx) => {
                        // 计算此结果的全局索引
                        const globalIndex = globalStartIndex + matchIdx;
                        const isActive = currentResultIndex === globalIndex;

                        return (
                          <div
                            key={`match-${fileResult.fileIndex}-${match.messageIndex}`}
                            className={`py-2 px-3 hover:bg-blue-50 cursor-pointer border-b border-gray-100 ${
                              isActive ? 'bg-blue-100' : ''
                            }`}
                            onClick={() => handleResultClick(fileResult.fileIndex, match.messageIndex, globalIndex)}
                          >
                            <div className="text-xs text-gray-500 mb-1 flex justify-between">
                              <span>消息 #{match.messageIndex + 1}</span>
                              {isActive && (
                                <span className="text-blue-600 font-medium">当前选中</span>
                              )}
                            </div>
                            <div className="text-sm">
                              {Array.isArray(match.highlightedPreview) ? (
                                match.highlightedPreview.map((part, i) => (
                                  <span
                                    key={i}
                                    className={part.highlight ? 'bg-yellow-200 font-medium' : ''}
                                  >
                                    {part.text}
                                  </span>
                                ))
                              ) : (
                                match.preview
                              )}
                              {match.preview && match.preview.length > 0 ? '...' : '(空内容)'}
                            </div>
                          </div>
                        );
                      })}
                    </div>
                  )}
                </div>
              );
            })}
          </div>
        )}
      </div>

      {/* 提示信息 - 当有导航正在进行时显示 */}
      {isNavigating && (
        <div className="fixed inset-0 bg-black bg-opacity-20 flex items-center justify-center z-[60]">
          <div className="bg-white p-4 rounded-lg shadow-lg flex items-center space-x-3">
            <Loader className="animate-spin h-5 w-5 text-blue-500" />
            <span>正在导航到搜索结果...</span>
          </div>
        </div>
      )}
      </div>
    </>
  );
};

export default SearchPanel;