// SearchManager.js 修复版 - 优化搜索功能并防止状态冲突
// nextResult 和 prevResult 现在只在当前文件内导航
// broadcastSearchUpdate 现在包含当前文件的本地搜索计数

import { useState, useEffect, useRef } from 'react';

const SearchManager = ({
  searchQuery,
  setSearchQuery,
  files,
  currentFileIndex, 
  processedData,
  setSelectedMessageIndices,
  setCurrentFileIndex,
  setIsSearching, 
  setShowSearchPanel
}) => {
  const [globalResults, setGlobalResults] = useState([]);
  const [activeResultIndex, setActiveResultIndex] = useState(-1); // Global index
  const [searching, setSearching] = useState(false); 
  const [lastQuery, setLastQuery] = useState('');
  const [localResultsCache, setLocalResultsCache] = useState({});
  const [searchSessionId, setSearchSessionId] = useState(null);
  
  const isNavigatingRef = useRef(false);
  const isMountedRef = useRef(true);
  const lastValidResultRef = useRef(null);
  const fileLoadingRef = useRef(false);
  const fileSwitchRequestIdRef = useRef(0);
  const fileSwitchTimeoutRef = useRef(null);
  const navigationDebounceTimerRef = useRef(null);
  const navigationAttemptCountRef = useRef(0);
  const cumulativeResultCountRef = useRef({});
  
  useEffect(() => {
    isMountedRef.current = true;
    return () => {
      isMountedRef.current = false;
      isNavigatingRef.current = false;
      if (fileSwitchTimeoutRef.current) clearTimeout(fileSwitchTimeoutRef.current);
      if (navigationDebounceTimerRef.current) clearTimeout(navigationDebounceTimerRef.current);
    };
  }, []);

  const safeSetState = (setter, value) => {
    if (isMountedRef.current) {
      try {
        setter(value);
      } catch (error) {
        console.error("设置状态失败:", error);
      }
    }
  };

   // 添加防抖函数，避免频繁触发搜索状态更新
  const debouncedBroadcastTimerRef = useRef(null);
  
  const broadcastSearchUpdate = (query = null, results = null, newActiveIndex = null) => {
    if (!isMountedRef.current) return;
    
    // 清除之前的定时器
    if (debouncedBroadcastTimerRef.current) {
      clearTimeout(debouncedBroadcastTimerRef.current);
    }
    
    // 使用setTimeout创建防抖
    debouncedBroadcastTimerRef.current = setTimeout(() => {
      try {
        const finalResults = results !== null ? results : globalResults;
        const finalQuery = query !== null ? query : searchQuery;
        const finalActiveIndex = newActiveIndex !== null ? newActiveIndex : activeResultIndex;
  
        // Calculate local counts for the current file
        let localTotalCount = 0;
        let localDisplayIndex = 0; // 1-based index for UI
        let localActiveResultIndexInFile = -1; // 0-based index within local results
  
        if (finalResults.length > 0 && currentFileIndex !== -1) {
          const localResultsInCurrentFile = finalResults.filter(r => r.fileIndex === currentFileIndex);
          localTotalCount = localResultsInCurrentFile.length;
  
          if (finalActiveIndex >= 0 && finalActiveIndex < finalResults.length) {
            const activeGlobalResult = finalResults[finalActiveIndex];
            if (activeGlobalResult && activeGlobalResult.fileIndex === currentFileIndex) {
              localActiveResultIndexInFile = localResultsInCurrentFile.findIndex(
                lr => lr.messageIndex === activeGlobalResult.messageIndex
              );
              if (localActiveResultIndexInFile !== -1) {
                localDisplayIndex = localActiveResultIndexInFile + 1;
              }
            }
          }
        }
  
        if (!isMountedRef.current) return; // 再次检查组件是否已卸载
  
        const event = new CustomEvent('search-status-update', {
          detail: {
            results: finalResults,
            activeIndex: finalActiveIndex,
            totalCount: finalResults.length, // Global total
            displayIndex: finalActiveIndex >= 0 ? finalActiveIndex + 1 : 0, // Global display index
            query: finalQuery,
            currentResult: finalActiveIndex >= 0 && finalActiveIndex < finalResults.length ? finalResults[finalActiveIndex] : null,
            searchSessionId: searchSessionId,
            localTotalCount: localTotalCount, // Count of matches in current file
            localDisplayIndex: localDisplayIndex, // 1-based index of active match in current file, or 0
          }
        });
        window.dispatchEvent(event);
        
        // 只有在实际调用broadcastSearchUpdate时才设置搜索状态为false
        if (typeof setIsSearching === 'function') {
          setIsSearching(false);
        }
      } catch (error) {
        console.error("广播搜索状态更新出错:", error);
        if (typeof setIsSearching === 'function') {
          setIsSearching(false); 
        }
      }
    }, 10); // 短延迟以允许批处理更新
  };

  useEffect(() => {
    window.searchManager = {
      getState: () => ({
        results: globalResults,
        activeIndex: activeResultIndex,
        totalCount: globalResults.length,
        displayIndex: activeResultIndex >= 0 ? activeResultIndex + 1 : 0,
        query: searchQuery,
        searchSessionId: searchSessionId,
        currentResult: activeResultIndex >= 0 ? globalResults[activeResultIndex] : null,
        currentFileIndex: currentFileIndex, // Expose currentFileIndex for waitForFileLoaded
      }),
      nextResult: () => {
        if (isNavigatingRef.current || globalResults.length === 0) return;
        const localResultsInCurrentFile = globalResults.filter(r => r.fileIndex === currentFileIndex);
        if (localResultsInCurrentFile.length === 0) return;
        let currentLocalIndex = -1;
        if (activeResultIndex !== -1 && globalResults[activeResultIndex].fileIndex === currentFileIndex) {
            currentLocalIndex = localResultsInCurrentFile.findIndex(
                r => r.messageIndex === globalResults[activeResultIndex].messageIndex
            );
        }
        const nextLocalIndex = (currentLocalIndex === -1 || currentLocalIndex === localResultsInCurrentFile.length - 1)
            ? 0
            : currentLocalIndex + 1;
        const targetLocalResult = localResultsInCurrentFile[nextLocalIndex];
        const newGlobalIndex = globalResults.findIndex(
            r => r.fileIndex === targetLocalResult.fileIndex && r.messageIndex === targetLocalResult.messageIndex
        );
        if (newGlobalIndex !== -1) {
            navigationAttemptCountRef.current = 0;
            safeNavigateToResult(newGlobalIndex);
        }
      },
      prevResult: () => {
        if (isNavigatingRef.current || globalResults.length === 0) return;
        const localResultsInCurrentFile = globalResults.filter(r => r.fileIndex === currentFileIndex);
        if (localResultsInCurrentFile.length === 0) return;
        let currentLocalIndex = -1;
        if (activeResultIndex !== -1 && globalResults[activeResultIndex].fileIndex === currentFileIndex) {
            currentLocalIndex = localResultsInCurrentFile.findIndex(
                r => r.messageIndex === globalResults[activeResultIndex].messageIndex
            );
        }
        const prevLocalIndex = (currentLocalIndex === -1 || currentLocalIndex === 0)
            ? localResultsInCurrentFile.length - 1
            : currentLocalIndex - 1;
        const targetLocalResult = localResultsInCurrentFile[prevLocalIndex];
        const newGlobalIndex = globalResults.findIndex(
            r => r.fileIndex === targetLocalResult.fileIndex && r.messageIndex === targetLocalResult.messageIndex
        );
        if (newGlobalIndex !== -1) {
            navigationAttemptCountRef.current = 0;
            safeNavigateToResult(newGlobalIndex);
        }
      },
      goToResult: (index) => {
        if (isNavigatingRef.current) return;
        if (index >= 0 && index < globalResults.length) {
          if (navigationDebounceTimerRef.current) clearTimeout(navigationDebounceTimerRef.current);
          navigationDebounceTimerRef.current = setTimeout(() => {
            navigationAttemptCountRef.current = 0;
            safeNavigateToResult(index);
          }, 100);
        }
      },
      search: (query) => {
        if (query === searchQuery && isNavigatingRef.current) return;
        setSearchQuery(query); 
        if (query !== lastQuery) { 
            const newSessionId = Date.now().toString();
            safeSetState(setSearchSessionId, newSessionId);
            cumulativeResultCountRef.current = {};
        }
      },
      clearSearch: () => {
        setSearchQuery('');
        safeSetState(setLastQuery, ''); 
        safeSetState(setGlobalResults, []);
        safeSetState(setActiveResultIndex, -1);
        safeSetState(setSearchSessionId, null);
        safeSetState(setLocalResultsCache, {});
        cumulativeResultCountRef.current = {};
        const clearEvent = new CustomEvent('search-status-update', {
          detail: {
            clearSearch: true,
            totalCount: 0, displayIndex: 0, activeIndex: -1, query: '', results: [],
            localTotalCount: 0, localDisplayIndex: 0 // Also clear local counts
          }
        });
        window.dispatchEvent(clearEvent);
        console.log("搜索状态已清除");
        return true;
      },
      hasResults: () => globalResults.length > 0,
      getTotalCount: () => globalResults.length,
      getCurrentIndex: () => activeResultIndex >= 0 ? activeResultIndex + 1 : 0,
      refreshCurrentFileCache: () => {
        if (searchQuery && searchQuery.trim() && currentFileIndex >= 0) {
          const keysToRemove = Object.keys(localResultsCache).filter(key =>
            key.startsWith(`${currentFileIndex}_`)
          );
          if (keysToRemove.length > 0) {
            const newCache = { ...localResultsCache };
            keysToRemove.forEach(key => delete newCache[key]);
            safeSetState(setLocalResultsCache, newCache);
            setTimeout(() => {
              if (searchQuery.trim() && isMountedRef.current) {
                performSearch(searchQuery);
              }
            }, 100);
          }
        }
      }
    };
    return () => {
      delete window.searchManager;
    };
  }, [globalResults, activeResultIndex, searchQuery, currentFileIndex, searchSessionId, setIsSearching, processedData, files, setCurrentFileIndex, setSelectedMessageIndices]); 
  
  const validateResult = (result) => {
    if (!result) return false;
    if (result.fileIndex < 0 || result.fileIndex >= files.length) return false;
    if (result.fileIndex === currentFileIndex && processedData) {
      if (!processedData.chat_history) return false;
      return processedData.chat_history.some(msg => msg.index === result.messageIndex);
    }
    return true; // For results in other files, assume valid for now
  };

  const waitForFileLoaded = (targetFileIndex, requestId) => {
    return new Promise((resolve, reject) => {
      if (requestId !== fileSwitchRequestIdRef.current) {
        reject(new Error("文件切换请求已过期"));
        return;
      }
      // If App.js has already switched and processedData is for the target file
      if (currentFileIndex === targetFileIndex && processedData && processedData.meta_info.uuid === files[targetFileIndex]?.name) { // A loose check; ideally UUID match
        resolve();
        return;
      }
      fileLoadingRef.current = true;
      if (fileSwitchTimeoutRef.current) clearTimeout(fileSwitchTimeoutRef.current);
      fileSwitchTimeoutRef.current = setTimeout(() => {
        if (fileLoadingRef.current && isMountedRef.current) {
          fileLoadingRef.current = false;
          reject(new Error("文件加载超时"));
        }
      }, 5000);
      
      safeSetState(setCurrentFileIndex, targetFileIndex); // Request App.js to switch file

      let checkCount = 0;
      const maxChecks = 100; // Increased checks, ~10 seconds
      const checkInterval = 100;

      const checkFileLoaded = () => {
        if (!isMountedRef.current || requestId !== fileSwitchRequestIdRef.current) {
          clearTimeout(fileSwitchTimeoutRef.current);
          reject(new Error("组件已卸载或请求已过期"));
          return;
        }
        checkCount++;
        // Check if the currentFileIndex prop (from App.js) has been updated to targetFileIndex
        // AND processedData is now available for that file.
        if (currentFileIndex === targetFileIndex && processedData) {
            // Further check if processedData actually corresponds to targetFileIndex if possible
            // This might require comparing a UUID or filename if available in processedData.meta_info
            const currentFileFromFilesArray = files[targetFileIndex];
            if (currentFileFromFilesArray && processedData.meta_info && processedData.meta_info.title === currentFileFromFilesArray.name.replace('.json','')) { // Example check
                clearTimeout(fileSwitchTimeoutRef.current);
                fileLoadingRef.current = false;
                setTimeout(resolve, 50); // Short delay for UI to settle
                return;
            } else if (!currentFileFromFilesArray) { // If files array changed
                 clearTimeout(fileSwitchTimeoutRef.current);
                 fileLoadingRef.current = false;
                 reject(new Error("文件列表已更改，无法确认文件加载"));
                 return;
            }
        }

        if (checkCount >= maxChecks) {
          clearTimeout(fileSwitchTimeoutRef.current);
          fileLoadingRef.current = false;
          reject(new Error("文件加载检查次数超过上限"));
          return;
        }
        setTimeout(checkFileLoaded, checkInterval);
      };
      setTimeout(checkFileLoaded, checkInterval); // Start checking
    });
  };

  const findNextValidResult = (currentIndex, direction = 1) => {
    if (globalResults.length === 0) return -1;
    for (let i = 1; i <= globalResults.length; i++) {
      const nextIndex = (currentIndex + i * direction + globalResults.length) % globalResults.length;
      if (validateResult(globalResults[nextIndex])) return nextIndex;
    }
    return -1;
  };

  const safeNavigateToResult = async (index) => {
    if (isNavigatingRef.current) return;
    if (index < 0 || index >= globalResults.length) return;
    
    navigationAttemptCountRef.current++;
    if (navigationAttemptCountRef.current > globalResults.length + 10) { // Increased threshold
      console.error("导航尝试次数过多", navigationAttemptCountRef.current);
      safeSetState(setSearching, false); 
      if(typeof setIsSearching === 'function') setIsSearching(false);
      isNavigatingRef.current = false;
      navigationAttemptCountRef.current = 0;
      return;
    }
    isNavigatingRef.current = true;
    safeSetState(setSearching, true);
    if(typeof setIsSearching === 'function') setIsSearching(true);

    try {
      const result = globalResults[index];
      if (!validateResult(result)) {
        const nextValidIndex = findNextValidResult(index);
        if (nextValidIndex !== -1 && nextValidIndex !== index) {
          safeSetState(setActiveResultIndex, nextValidIndex);
          broadcastSearchUpdate(null, null, nextValidIndex);
          setTimeout(() => {
            isNavigatingRef.current = false; 
            safeNavigateToResult(nextValidIndex);
          }, 50); // Slightly longer delay
          return;
        } else {
          if (lastValidResultRef.current) {
            const { fileIndex: lvfFileIndex, messageIndex: lvfMessageIndex } = lastValidResultRef.current;
            if (lvfFileIndex === currentFileIndex) { 
              safeSetState(setSelectedMessageIndices, [lvfMessageIndex]);
              broadcastSearchUpdate(null, null, activeResultIndex); 
            }
          }
          safeSetState(setSearching, false);
          if(typeof setIsSearching === 'function') setIsSearching(false);
          isNavigatingRef.current = false;
          return;
        }
      }
      safeSetState(setActiveResultIndex, index);
      // Broadcast update *before* potential file switch
      broadcastSearchUpdate(null, null, index); 
      
      if (result.fileIndex !== currentFileIndex) { 
        const requestId = Date.now();
        fileSwitchRequestIdRef.current = requestId;
        try {
          await waitForFileLoaded(result.fileIndex, requestId);
          if (!isMountedRef.current || requestId !== fileSwitchRequestIdRef.current) {
            isNavigatingRef.current = false;
            safeSetState(setSearching, false);
            if(typeof setIsSearching === 'function') setIsSearching(false);
            return;
          }
          lastValidResultRef.current = { ...result };
          // After file switch, processedData and currentFileIndex are updated by App.js
          // We need to ensure selection happens after that.
          setTimeout(() => { // Delay to allow App.js to update processedData
            if (!isMountedRef.current) return;
            try {
              safeSetState(setSelectedMessageIndices, [result.messageIndex]);
              // broadcastSearchUpdate(null, null, index); // Broadcast again after selection if needed
              setTimeout(() => {
                if (isMountedRef.current) {
                  safeSetState(setSearching, false);
                  if(typeof setIsSearching === 'function') setIsSearching(false);
                  isNavigatingRef.current = false;
                  navigationAttemptCountRef.current = 0;
                }
              }, 100); // Reduced delay
            } catch (err) {
              console.error("设置选中消息时出错:", err);
              safeSetState(setSearching, false);
              if(typeof setIsSearching === 'function') setIsSearching(false);
              isNavigatingRef.current = false;
            }
          }, 100); // Delay for App.js to process file change
        } catch (error) {
          console.error("等待文件加载时出错:", error);
          if (isMountedRef.current) {
            safeSetState(setSearching, false);
            if(typeof setIsSearching === 'function') setIsSearching(false);
            isNavigatingRef.current = false;
          }
        }
      } else { // Same file navigation
        lastValidResultRef.current = { ...result };
        safeSetState(setSelectedMessageIndices, [result.messageIndex]);
        // broadcastSearchUpdate(null, null, index); // Already broadcasted before potential file switch
        setTimeout(() => {
          if (isMountedRef.current) {
            safeSetState(setSearching, false);
            if(typeof setIsSearching === 'function') setIsSearching(false);
            isNavigatingRef.current = false;
            navigationAttemptCountRef.current = 0;
          }
        }, 50); // Reduced delay
      }
    } catch (error) {
      console.error("导航到搜索结果时出错:", error);
      if (isMountedRef.current) {
        safeSetState(setSearching, false);
        if(typeof setIsSearching === 'function') setIsSearching(false);
        isNavigatingRef.current = false;
        navigationAttemptCountRef.current = 0;
      }
    }
  };

  useEffect(() => {
    const handleSearchIndexUpdate = (e) => {
      if (!isMountedRef.current || isNavigatingRef.current) return;
      const { index } = e.detail;
      if (typeof index === 'number' && index >= 0 && index < globalResults.length) {
        safeSetState(setActiveResultIndex, index);
        broadcastSearchUpdate(null, null, index); 
      }
    };
    window.addEventListener('search-index-update', handleSearchIndexUpdate);
    return () => window.removeEventListener('search-index-update', handleSearchIndexUpdate);
  }, [globalResults]); 
  
  useEffect(() => {
    if (navigationDebounceTimerRef.current) {
        clearTimeout(navigationDebounceTimerRef.current);
    }
    navigationDebounceTimerRef.current = setTimeout(() => {
        if (searchQuery !== lastQuery) { 
            if (searchQuery.trim()) {
                performSearch(searchQuery);
            } else {
                safeSetState(setGlobalResults, []);
                safeSetState(setActiveResultIndex, -1);
                broadcastSearchUpdate(searchQuery, [], -1); 
            }
            safeSetState(setLastQuery, searchQuery); 
        } else if (searchQuery.trim() && globalResults.length === 0 && files.length > 0) {
            // If query is the same but no results (e.g., after file load), try searching again
            performSearch(searchQuery);
        }
    }, 300); 

    return () => {
        if (navigationDebounceTimerRef.current) {
            clearTimeout(navigationDebounceTimerRef.current);
        }
    };
  }, [searchQuery, files, currentFileIndex]); // Added files and currentFileIndex to re-trigger search if needed

  const performSearch = async (query) => {
    if (!query || !query.trim() || !isMountedRef.current) {
      safeSetState(setGlobalResults, []);
      safeSetState(setActiveResultIndex, -1);
      broadcastSearchUpdate(query, [], -1); 
      return;
    }
    
    safeSetState(setSearching, true);
    if(typeof setIsSearching === 'function') setIsSearching(true);

    const results = [];
    const lowerQuery = query.toLowerCase();
    try {
      const currentFileResults = [];
      const otherFileResults = [];
      if (processedData && processedData.chat_history && currentFileIndex !== -1) { 
        const cacheKey = `${currentFileIndex}_${query}`;
        if (localResultsCache[cacheKey]) {
          currentFileResults.push(...localResultsCache[cacheKey]);
        } else {
          const matches = processedData.chat_history
            .filter(msg => msg.display_text && msg.display_text.toLowerCase().includes(lowerQuery))
            .map(msg => ({
              type: 'local',
              fileIndex: currentFileIndex,
              messageIndex: msg.index,
              preview: msg.display_text ? msg.display_text.substring(0, 100) : '',
              sessionId: searchSessionId 
            }));
          if (matches.length > 0) {
            currentFileResults.push(...matches);
            safeSetState(setLocalResultsCache, prev => ({
              ...prev,
              [cacheKey]: [...matches] 
            }));
          }
        }
      }
      if (files.length > 1) {
        const otherFilesPromises = [];
        for (let i = 0; i < files.length; i++) {
          if (i === currentFileIndex) continue; 
          const file = files[i];
          const cacheKey = `${i}_${query}`;
          if (localResultsCache[cacheKey]) {
            otherFileResults.push(...localResultsCache[cacheKey]);
            continue;
          }
          const searchPromise = (async () => {
            try {
              const text = await file.text();
              if (!isMountedRef.current) return []; 
              const jsonData = JSON.parse(text);
              const utils = await import('../utils'); 
              const data = utils.extractChatData(jsonData);
              const matches = data.chat_history
                .filter(msg => msg.display_text && msg.display_text.toLowerCase().includes(lowerQuery))
                .map(msg => ({
                  type: 'cross-file',
                  fileIndex: i,
                  messageIndex: msg.index,
                  preview: msg.display_text ? msg.display_text.substring(0, 100) : '',
                  sessionId: searchSessionId 
                }));
              return matches;
            } catch (err) {
              console.error(`搜索文件 ${file.name} 出错:`, err);
              return [];
            }
          })();
          otherFilesPromises.push(searchPromise);
        }
        const otherFilesResultsSettled = await Promise.allSettled(otherFilesPromises);
        otherFilesResultsSettled.forEach((settledResult, index) => {
          if (settledResult.status === 'fulfilled' && settledResult.value.length > 0) {
            const matches = settledResult.value;
            const fileIndex = Array.from(files.keys())
              .filter(idx => idx !== currentFileIndex)[index];
            if (fileIndex !== undefined) {
              const cacheKey = `${fileIndex}_${query}`;
              safeSetState(setLocalResultsCache, prev => ({
                ...prev,
                [cacheKey]: [...matches] 
              }));
              otherFileResults.push(...matches);
            }
          }
        });
      }
      if (!isMountedRef.current) return;
      results.push(...currentFileResults, ...otherFileResults);
      const uniqueResults = [];
      const seen = new Set();
      for (const result of results) {
        const key = `${result.fileIndex}_${result.messageIndex}`;
        if (!seen.has(key)) {
          seen.add(key);
          uniqueResults.push(result);
        }
      }
      
      uniqueResults.sort((a, b) => {
        if (a.fileIndex === currentFileIndex && b.fileIndex !== currentFileIndex) return -1;
        if (a.fileIndex !== currentFileIndex && b.fileIndex === currentFileIndex) return 1;
        if (a.fileIndex !== b.fileIndex) return a.fileIndex - b.fileIndex;
        return a.messageIndex - b.messageIndex;
      });

      safeSetState(setGlobalResults, uniqueResults);
      if (uniqueResults.length > 0) {
        // Prioritize setting active index to a result in the current file if one exists
        let initialIndex = uniqueResults.findIndex(r => r.fileIndex === currentFileIndex && validateResult(r));
        if (initialIndex === -1) { // If no result in current file, default to the first overall result
            initialIndex = 0;
        }
        safeSetState(setActiveResultIndex, initialIndex);
        broadcastSearchUpdate(query, uniqueResults, initialIndex);
      } else {
        safeSetState(setActiveResultIndex, -1);
        broadcastSearchUpdate(query, [], -1); 
      }
    } catch (err) {
      console.error('执行搜索出错:', err);
      safeSetState(setGlobalResults, []);
      safeSetState(setActiveResultIndex, -1);
      broadcastSearchUpdate(query, [], -1); 
    } finally {
      if (isMountedRef.current) {
        safeSetState(setSearching, false);
        if(typeof setIsSearching === 'function') setIsSearching(false);
      }
    }
  };
 
  const sortAndUpdateResults = () => {
    if (globalResults.length === 0 || !isMountedRef.current) return;
    const reorderedResults = [...globalResults].sort((a, b) => {
      if (a.fileIndex === currentFileIndex && b.fileIndex !== currentFileIndex) return -1;
      if (a.fileIndex !== currentFileIndex && b.fileIndex === currentFileIndex) return 1;
      if (a.fileIndex !== b.fileIndex) return a.fileIndex - b.fileIndex; 
      return a.messageIndex - b.messageIndex; 
    });
    safeSetState(setGlobalResults, reorderedResults);
    
    let newActiveIndex = -1;
    // Try to find the first result in the current file
    const currentFileFirstResultIndex = reorderedResults.findIndex(
        r => r.fileIndex === currentFileIndex && validateResult(r)
    );

    if (currentFileFirstResultIndex !== -1) {
        newActiveIndex = currentFileFirstResultIndex;
    } else if (reorderedResults.length > 0) {
        // If no results in current file, but there are global results, point to the first global result
        newActiveIndex = 0; 
    }
    
    safeSetState(setActiveResultIndex, newActiveIndex);
    broadcastSearchUpdate(null, reorderedResults, newActiveIndex); 
  };
 
  useEffect(() => {
    const handleSearchNavigation = (e) => {
      if (isNavigatingRef.current) return;
      const { action, index, query: navQuery } = e.detail; 
      switch (action) {
        case 'next': window.searchManager.nextResult(); break;
        case 'prev': window.searchManager.prevResult(); break;
        case 'goto': if (typeof index === 'number') window.searchManager.goToResult(index); break;
        case 'search': if (navQuery !== undefined) window.searchManager.search(navQuery); break;
        case 'clear': window.searchManager.clearSearch(); break;
        case 'open-panel': if (typeof setShowSearchPanel === 'function') setShowSearchPanel(true); break;
        default: break;
      }
    };
    window.addEventListener('search-navigation', handleSearchNavigation);
    return () => window.removeEventListener('search-navigation', handleSearchNavigation);
  }, [setShowSearchPanel]); 
  
  useEffect(() => {
    if (fileLoadingRef.current) fileLoadingRef.current = false;
    isNavigatingRef.current = false; // Reset navigation lock on file change
    
    // If there are existing global results, re-sort them with the new currentFileIndex priority
    if (globalResults.length > 0) {
      setTimeout(sortAndUpdateResults, 50); // Delay to ensure currentFileIndex prop is updated
    }
    
    // If a search query exists, re-trigger performSearch to ensure results are up-to-date for the new file
    // and that local cache is correctly populated/used.
    if (searchQuery.trim()) {
        setTimeout(() => {
            if (isMountedRef.current && searchQuery.trim()) {
                // Force a re-search for the current query.
                // This ensures that if the user switched to a file that wasn't previously searched
                // for this query, it gets searched now.
                // It also helps if the previous search was for a different file.
                performSearch(searchQuery);
            }
        }, 100); // Delay to ensure file processing in App.js might have started/completed
    }
  }, [currentFileIndex, processedData]); // Listen to processedData to ensure file is loaded

  return null;
};
export default SearchManager;
