// App.js 修复版 - 解决分支管理、消息选择和搜索间的冲突问题
// 同时修复底部功能区按钮点击行为，并根据有无分支数据显示/隐藏分支管理器
// 搜索浮窗的上一条/下一条按钮现在只在当前文件内跳转
// 搜索浮窗计数器显示当前文件内的匹配计数

import JSZip from 'jszip';
import React, { useState, useEffect, useRef, useCallback } from 'react';
// Assuming utils.js is in the same directory or ./utils/ folder
// Adjust the path if your structure is different
import { extractChatData, detectBranches, exportChatAsMarkdown, saveTextFile } from './utils'; // Or './utils/utils'

// Assuming components are in a ./components/ folder
// Adjust paths if needed
import MessageList from './components/MessageList';
import MessageDetail from './components/MessageDetail';
import ExportPanel from './components/ExportPanel';
import MetadataPanel from './components/MetadataPanel';
import BranchStatsPanel from './components/BranchStatsPanel';
import BranchManager from './components/BranchManager';
import WelcomePage from './components/WelcomePage';
import UserSettingsPanel, { loadSettings, saveSettings } from './components/UserSettings';
import SearchPanel from './components/SearchPanel';

// Assuming SearchManager is in the same directory or ./features/search/ folder
// Adjust the path if needed
import SearchManager from './components/SearchManager'; // Or './features/search/SearchManager'

// Assuming App.css is in the same directory or ./styles/ folder
// Adjust path if needed
import './App.css'; // Or './styles/App.css'

import { Settings, Search, CheckSquare, Download, Clipboard, ChevronDown, ChevronUp, /*Info,*/ ChevronRight, PlusCircle, Trash2, ChevronLeft, FileText, X } from 'lucide-react'; // Removed Info

const App = () => {
  // --- Core State ---
  const [files, setFiles] = useState([]);
  const [currentFileIndex, setCurrentFileIndex] = useState(0);
  const [processedData, setProcessedData] = useState(null);
  const [statusMessage, setStatusMessage] = useState('就绪');

  // --- Selection State ---
  const [selectedMessageIndices, setSelectedMessageIndices] = useState([]);
  const [selectedBranchIndices, setSelectedBranchIndices] = useState([]);
  
  // 跟踪最后操作的部分 - 用于协调选择冲突
  // const [lastSelectionSource, setLastSelectionSource] = useState(null); // Removed as unused
  const [, setLastSelectionSource] = useState(null); // Kept setter for potential future use, but variable itself is removed

  // --- UI State ---
  const [activeTab, setActiveTab] = useState('content');
  const [activeBottomPanelTab, setActiveBottomPanelTab] = useState('branches');
  const [showBottomPanel, setShowBottomPanel] = useState(true);
  // const [bottomPanelAnimating, setBottomPanelAnimating] = useState(false); // Removed as unused
  // const [, setBottomPanelAnimating] = useState(false); // Kept setter for potential future use

  const [showSettingsPanel, setShowSettingsPanel] = useState(false);
  const [showSearchPanel, setShowSearchPanel] = useState(false);

  // --- Search State ---
  const [searchQuery, setSearchQuery] = useState('');
  const [isSearching, setIsSearching] = useState(false);
  // State to hold search results info for UI display (both global and local for current file)
  const [searchNavInfo, setSearchNavInfo] = useState({
      globalTotalCount: 0,
      globalDisplayIndex: 0, 
      globalActiveIndex: -1,
      localTotalCount: 0,     // Matches in the current file
      localDisplayIndex: 0,   // Active index within current file's matches (1-based)
      hasGlobalResults: false,
      hasLocalResults: false,
  });

  // --- Marking State (Per File) ---
  const [fileMarkedItems, setFileMarkedItems] = useState(new Map());
  const [fileImportantItems, setFileImportantItems] = useState(new Map());
  const [fileDeletedItems, setFileDeletedItems] = useState(new Map());

  // --- Marking State (Current File) ---
  const [markedItems, setMarkedItems] = useState(new Set());
  const [importantItems, setImportantItems] = useState(new Set());
  const [deletedItems, setDeletedItems] = useState(new Set());
  const [showCompleted, setShowCompleted] = useState(true);
  const [showDeleted, setShowDeleted] = useState(false);

  // --- Undo/Redo State ---
  const [undoStack, setUndoStack] = useState([]);
  const [redoStack, setRedoStack] = useState([]);

  // --- Configuration State ---
  const [userSettings, setUserSettings] = useState(loadSettings());
  const [exportConfig, setExportConfig] = useState({
    includeThinking: true,
    includeTools: true,
    includeArtifacts: true,
    includeCitations: true,
    exportObsidianMetadata: true,
    hideTimestamps: false,
    exportMarkedOnly: false,
    obsidianProperties: [],
    obsidianTags: []
  });
  const [obsidianTags, setObsidianTags] = useState([]);
  const [obsidianProperties, setObsidianProperties] = useState([]);

  // --- Refs ---
  const fileInputRef = useRef(null);
  const rightPanelRef = useRef(null);
  const messageDetailRef = useRef(null);
  const isMountedRef = useRef(true); // 添加挂载状态跟踪
  const ctrlKeyPressedRef = useRef(false); // 跟踪Ctrl键按下状态
  // const lastSearchResultRef = useRef(null); // Removed as unused

  // --- 键盘事件监听 ---
  useEffect(() => {
    const handleKeyDown = (e) => {
      if (e.ctrlKey || e.metaKey) {
        ctrlKeyPressedRef.current = true;
      }
    };
    
    const handleKeyUp = (e) => {
      if (!e.ctrlKey && !e.metaKey) {
        ctrlKeyPressedRef.current = false;
      }
    };
    
    window.addEventListener('keydown', handleKeyDown);
    window.addEventListener('keyup', handleKeyUp);
    
    return () => {
      window.removeEventListener('keydown', handleKeyDown);
      window.removeEventListener('keyup', handleKeyUp);
    };
  }, []);

  // --- Helper Functions (saveMarkState, loadMarkState) ---
  /**
   * Saves the marking state for a given file UUID to localStorage.
   */
   const saveMarkState = useCallback((fileUuid, markedSet, importantSet, deletedSet) => {
    if (!fileUuid) return;
    const markState = {
      markedItems: Array.from(markedSet),
      importantItems: Array.from(importantSet),
      deletedItems: Array.from(deletedSet),
      timestamp: new Date().toISOString()
    };
    try {
      localStorage.setItem(`chat_marks_${fileUuid}`, JSON.stringify(markState));
    } catch (error) {
      console.error("保存标记状态出错:", error);
      setStatusMessage('保存标记状态失败');
    }
  }, []);

  /**
   * Loads marking state from localStorage for the given file UUID.
   */
  const loadMarkState = useCallback((fileUuid) => {
    if (!fileUuid) return;
    try {
      const savedState = localStorage.getItem(`chat_marks_${fileUuid}`);
      if (savedState) {
        const parsedState = JSON.parse(savedState);
        const loadedMarked = new Set(parsedState.markedItems || []);
        const loadedImportant = new Set(parsedState.importantItems || []);
        const loadedDeleted = new Set(parsedState.deletedItems || []);

        setMarkedItems(loadedMarked);
        setImportantItems(loadedImportant);
        setDeletedItems(loadedDeleted);

        setFileMarkedItems(prev => new Map(prev).set(fileUuid, Array.from(loadedMarked)));
        setFileImportantItems(prev => new Map(prev).set(fileUuid, Array.from(loadedImportant)));
        setFileDeletedItems(prev => new Map(prev).set(fileUuid, Array.from(loadedDeleted)));
        return true;
      }
    } catch (error) {
      console.error("加载标记状态出错:", error);
      setStatusMessage('加载标记状态失败');
    }
    setMarkedItems(new Set());
    setImportantItems(new Set());
    setDeletedItems(new Set());
    return false;
  }, []);


  // --- Effects ---

  // 设置组件挂载状态
  useEffect(() => {
    isMountedRef.current = true;
    return () => {
      isMountedRef.current = false;
    };
  }, []);

  // Process the currently selected file
  useEffect(() => {
    const processCurrentFile = async () => {
      if (!files || files.length === 0 || currentFileIndex >= files.length) {
        setProcessedData(null);
        return;
      }
      const file = files[currentFileIndex];
      setStatusMessage(`处理文件: ${file.name}`);
      setIsSearching(true);
      try {
        const text = await file.text();
        if (!isMountedRef.current) return; // 检查组件是否仍然挂载
        
        const jsonData = JSON.parse(text);
        let data = extractChatData(jsonData);
        data = detectBranches(data);
        const fileUuid = data.meta_info.uuid;

        if (fileUuid) {
          loadMarkState(fileUuid);
        } else {
          setMarkedItems(new Set());
          setImportantItems(new Set());
          setDeletedItems(new Set());
        }
        
        // 获取当前最新的deletedItems状态而不是依赖它
        // 这样可以避免循环依赖问题
        setTimeout(() => {
          if (isMountedRef.current && data) {
            // 使用函数式更新确保获取最新状态
            setProcessedData(prevData => {
              if (!prevData) return data;
              return {
                ...data,
                deletedItems: Array.from(deletedItems)
              };
            });
          }
        }, 0);

        // 先设置基本数据，不包含deletedItems
        const initialData = { ...data };
        delete initialData.deletedItems; // 移除可能已存在的deletedItems
        setProcessedData(initialData);
        
        // 不再在文件加载时重置选择状态，允许跨文件保留选择
        // setSelectedMessageIndices([]);
        // setSelectedBranchIndices([]);
        setObsidianTags([]);
        setObsidianProperties([]);
        setUndoStack([]);
        setRedoStack([]);
        setStatusMessage(formatStatusMessage(`文件加载完成: ${file.name}`));
      } catch (error) {
        console.error("处理文件出错:", error);
        if (isMountedRef.current) {
          setStatusMessage(`文件处理错误: ${error.message}`);
          setProcessedData(null);
        }
      } finally {
        if (isMountedRef.current) {
          setIsSearching(false);
        }
      }
    };
    processCurrentFile();
  }, [files, currentFileIndex, loadMarkState]); // 移除deletedItems依赖，解决循环依赖问题

  // Update processedData when deletedItems state changes (for BranchManager display)
  // 使用useRef跟踪最后的deletedItems值，避免不必要的更新
  const lastDeletedItemsRef = useRef([]);
  
  useEffect(() => {
    // 只有当processedData存在且deletedItems真正变化时才更新
    if (processedData) {
      const currentDeletedItemsArray = Array.from(deletedItems);
      // 简单比较数组长度和内容是否变化
      const hasChanged = 
        lastDeletedItemsRef.current.length !== currentDeletedItemsArray.length ||
        JSON.stringify(lastDeletedItemsRef.current) !== JSON.stringify(currentDeletedItemsArray);
      
      if (hasChanged) {
        // 更新引用值
        lastDeletedItemsRef.current = currentDeletedItemsArray;
        
        // 使用函数式更新避免依赖processedData
        setProcessedData(prevData => {
          if (!prevData) return prevData;
          return {
            ...prevData,
            deletedItems: currentDeletedItemsArray
          };
        });
      }
    }
  }, [deletedItems]); // 移除processedData依赖，解决循环依赖问题

  // Apply user settings
   useEffect(() => {
    setShowCompleted(userSettings.behavior.showCompletedMessages);
    setShowDeleted(userSettings.behavior.showDeletedMessages);
    setExportConfig(prev => ({
      ...prev,
      includeThinking: userSettings.export.defaultIncludeThinking,
      includeTools: userSettings.export.defaultIncludeTools,
      includeArtifacts: userSettings.export.defaultIncludeArtifacts,
      includeCitations: userSettings.export.defaultIncludeCitations,
      exportObsidianMetadata: userSettings.export.defaultExportObsidianMetadata,
      obsidianTags: prev.obsidianTags,
      obsidianProperties: prev.obsidianProperties
    }));
  }, [userSettings]);

  // Update exportConfig when obsidian metadata changes
  useEffect(() => {
    setExportConfig(prev => ({ ...prev, obsidianTags, obsidianProperties }));
  }, [obsidianTags, obsidianProperties]);

  // Listen for global search status updates from SearchManager
  useEffect(() => {
    const handleSearchUpdate = (event) => {
      const { 
        totalCount, 
        displayIndex, 
        activeIndex, 
        query, 
        clearSearch: shouldClear,
        localTotalCount, // New: count of matches in current file
        localDisplayIndex // New: 1-based index of active match in current file
      } = event.detail;
      
      if (shouldClear === true) {
        setSearchNavInfo({
          globalTotalCount: 0, globalDisplayIndex: 0, globalActiveIndex: -1,
          localTotalCount: 0, localDisplayIndex: 0,
          hasGlobalResults: false, hasLocalResults: false,
        });
        // setSearchQuery(''); // SearchManager already clears its query, App might not need to
        return;
      }
      
      if (query === searchQuery || !searchQuery) {
        setSearchNavInfo({
          globalTotalCount: totalCount,
          globalDisplayIndex: displayIndex,
          globalActiveIndex: activeIndex,
          localTotalCount: localTotalCount !== undefined ? localTotalCount : 0,
          localDisplayIndex: localDisplayIndex !== undefined ? localDisplayIndex : 0,
          hasGlobalResults: totalCount > 0,
          hasLocalResults: localTotalCount !== undefined ? localTotalCount > 0 : false,
        });
        
        if (!searchQuery && query) {
          setSearchQuery(query);
        }
      }
    };
    
    window.addEventListener('search-status-update', handleSearchUpdate);
    return () => window.removeEventListener('search-status-update', handleSearchUpdate);
  }, [searchQuery]); 

  // Clear search info when files change or are removed
  useEffect(() => {
      if (files.length === 0) {
          setSearchQuery(''); // Also clear query if no files
      }
      // Reset all search navigation info
      setSearchNavInfo({
        globalTotalCount: 0, globalDisplayIndex: 0, globalActiveIndex: -1,
        localTotalCount: 0, localDisplayIndex: 0,
        hasGlobalResults: false, hasLocalResults: false,
      });
  }, [files, currentFileIndex]);

  const handleClearSearch = useCallback(() => {
    setSearchQuery('');
    setSearchNavInfo({
      globalTotalCount: 0, globalDisplayIndex: 0, globalActiveIndex: -1,
      localTotalCount: 0, localDisplayIndex: 0,
      hasGlobalResults: false, hasLocalResults: false,
    });
    setIsSearching(false);
    setShowSearchPanel(false);
    if (window.searchManager && typeof window.searchManager.clearSearch === 'function') {
      window.searchManager.clearSearch();
    }
    setStatusMessage('已清除搜索');
  }, []);

  // 监听ESC键关闭搜索功能
  useEffect(() => {
    const handleEscKeyPress = (e) => {
      if (e.key === 'Escape' && searchQuery.trim() !== '') {
        handleClearSearch();
      }
    };
    
    window.addEventListener('keydown', handleEscKeyPress);
    return () => window.removeEventListener('keydown', handleEscKeyPress);
  }, [searchQuery, handleClearSearch]); // Added handleClearSearch

  // --- Event Handlers ---
  const handleLoadClick = useCallback(() => fileInputRef.current?.click(), []);
  const handleFileChange = useCallback(async (event) => {
    const selectedFiles = event.target.files;
    if (!selectedFiles || selectedFiles.length === 0) return;
    const fileList = Array.from(selectedFiles).filter(file => file.name.endsWith('.json'));
    if (fileList.length === 0) {
      setStatusMessage('未找到JSON文件');
      return;
    }
    // Append new files
    setFiles(prevFiles => {
        const newFilesToAdd = fileList.filter(newFile => !prevFiles.some(existingFile => existingFile.name === newFile.name && existingFile.lastModified === newFile.lastModified));
        if (newFilesToAdd.length === 0) {
            setStatusMessage('选择的文件已加载');
            return prevFiles;
        }
        setStatusMessage(`已加载 ${newFilesToAdd.length} 个新文件`);
        return [...prevFiles, ...newFilesToAdd];
    });
    event.target.value = null; // Allow re-selecting same file
  }, []);

  const handleRemoveCurrentFile = useCallback(() => {
    if (files.length === 0) return;
    const fileToRemove = files[currentFileIndex];
    // const fileUuid = processedData?.meta_info?.uuid; // Removed as unused
    const newFiles = files.filter((_, index) => index !== currentFileIndex);

    if (processedData?.meta_info?.uuid) { // Check if fileUuid exists before using
      const fileUuidToRemove = processedData.meta_info.uuid;
      // Clear state maps for the removed file
      setFileMarkedItems(prev => { const m = new Map(prev); m.delete(fileUuidToRemove); return m; });
      setFileImportantItems(prev => { const m = new Map(prev); m.delete(fileUuidToRemove); return m; });
      setFileDeletedItems(prev => { const m = new Map(prev); m.delete(fileUuidToRemove); return m; });
      try { localStorage.removeItem(`chat_marks_${fileUuidToRemove}`); } catch (e) { console.error("Failed to remove marks from localStorage", e); }
    }

    if (newFiles.length === 0) {
      setFiles([]);
      setProcessedData(null);
      setSelectedMessageIndices([]);
      setSelectedBranchIndices([]);
      setCurrentFileIndex(0);
      setStatusMessage('已移除所有文件');
    } else {
      const newIndex = currentFileIndex >= newFiles.length ? newFiles.length - 1 : currentFileIndex;
      setFiles(newFiles);
      setCurrentFileIndex(newIndex); 
      setStatusMessage(`已移除文件 "${fileToRemove.name}"，剩余 ${newFiles.length} 个文件`);
    }
  }, [files, currentFileIndex, processedData?.meta_info?.uuid]); 

  const handlePrevFile = useCallback(() => currentFileIndex > 0 && setCurrentFileIndex(currentFileIndex - 1), [currentFileIndex]);
  const handleNextFile = useCallback(() => currentFileIndex < files.length - 1 && setCurrentFileIndex(currentFileIndex + 1), [currentFileIndex, files.length]);

  const handleMessageSelect = useCallback((index, isCtrlPressed) => {
    const ctrlPressed = isCtrlPressed || ctrlKeyPressedRef.current;
    setLastSelectionSource('message');
    setSelectedMessageIndices(prev => {
        if (ctrlPressed) {
            const newSelection = new Set(prev);
            if (newSelection.has(index)) newSelection.delete(index);
            else newSelection.add(index);
            return Array.from(newSelection).sort((a, b) => a - b);
        } else {
            return prev.length === 1 && prev[0] === index ? prev : [index]; 
        }
    });
    setSelectedBranchIndices([]);
  }, []);

  const saveStateForUndo = useCallback(() => {
    const currentState = { markedItems: new Set(markedItems), importantItems: new Set(importantItems), deletedItems: new Set(deletedItems) };
    setUndoStack(prev => [...prev.slice(-19), currentState]);
    setRedoStack([]);
  }, [markedItems, importantItems, deletedItems]);

  const updateMarkingState = useCallback((newState) => {
    const fileUuid = processedData?.meta_info?.uuid;
    if (!fileUuid) return;
    setMarkedItems(newState.markedItems);
    setImportantItems(newState.importantItems);
    setDeletedItems(newState.deletedItems);
    setFileMarkedItems(prev => new Map(prev).set(fileUuid, Array.from(newState.markedItems)));
    setFileImportantItems(prev => new Map(prev).set(fileUuid, Array.from(newState.importantItems)));
    setFileDeletedItems(prev => new Map(prev).set(fileUuid, Array.from(newState.deletedItems)));
    saveMarkState(fileUuid, newState.markedItems, newState.importantItems, newState.deletedItems);
  }, [processedData?.meta_info?.uuid, saveMarkState]);

  const handleUndo = useCallback(() => {
    if (undoStack.length === 0) return;
    const currentState = { markedItems: new Set(markedItems), importantItems: new Set(importantItems), deletedItems: new Set(deletedItems) };
    setRedoStack(prev => [...prev.slice(-19), currentState]);
    const prevState = undoStack[undoStack.length - 1];
    updateMarkingState(prevState);
    setUndoStack(prev => prev.slice(0, -1));
    setStatusMessage("已撤销操作");
  }, [undoStack, markedItems, importantItems, deletedItems, updateMarkingState]);

  const handleRedo = useCallback(() => {
    if (redoStack.length === 0) return;
    const currentState = { markedItems: new Set(markedItems), importantItems: new Set(importantItems), deletedItems: new Set(deletedItems) };
    setUndoStack(prev => [...prev.slice(-19), currentState]);
    const nextState = redoStack[redoStack.length - 1];
    updateMarkingState(nextState);
    setRedoStack(prev => prev.slice(0, -1));
    setStatusMessage("已重做操作");
  }, [redoStack, markedItems, importantItems, deletedItems, updateMarkingState]);

  const applyMarkToSelected = useCallback((markType) => {
    if (selectedMessageIndices.length === 0 || !processedData?.meta_info?.uuid) return;
    saveStateForUndo();
    const fileUuid = processedData.meta_info.uuid;
    let newMarked = new Set(markedItems);
    let newImportant = new Set(importantItems);
    let newDeleted = new Set(deletedItems);
    selectedMessageIndices.forEach(index => {
      if (markType === 'deleted') { newMarked.delete(index); newImportant.delete(index); newDeleted.add(index); }
      else if (markType === 'completed') { newDeleted.delete(index); newMarked.add(index); }
      else if (markType === 'important') { newDeleted.delete(index); newImportant.add(index); }
    });
    const newState = { markedItems: newMarked, importantItems: newImportant, deletedItems: newDeleted };
    updateMarkingState(newState);
    const count = selectedMessageIndices.length;
    let status = '';
    if (markType === 'completed') status = `已将 ${count} 条消息标记为已完成`;
    else if (markType === 'important') status = `已将 ${count} 条消息标记为重点`;
    else if (markType === 'deleted') status = `已将 ${count} 条消息标记为已删除`;
    setStatusMessage(status);
  }, [selectedMessageIndices, processedData?.meta_info?.uuid, markedItems, importantItems, deletedItems, saveStateForUndo, updateMarkingState]);

  const handleMarkCompleted = useCallback(() => applyMarkToSelected('completed'), [applyMarkToSelected]);
  const handleMarkImportant = useCallback(() => applyMarkToSelected('important'), [applyMarkToSelected]);
  const handleMarkDeleted = useCallback(() => applyMarkToSelected('deleted'), [applyMarkToSelected]);

  const handleClearMarks = useCallback(() => {
    if (selectedMessageIndices.length === 0 || !processedData?.meta_info?.uuid) return;
    saveStateForUndo();
    const newMarked = new Set(markedItems);
    const newImportant = new Set(importantItems);
    const newDeleted = new Set(deletedItems);
    selectedMessageIndices.forEach(index => { newMarked.delete(index); newImportant.delete(index); newDeleted.delete(index); });
    const newState = { markedItems: newMarked, importantItems: newImportant, deletedItems: newDeleted };
    updateMarkingState(newState);
    setStatusMessage(`已清除 ${selectedMessageIndices.length} 条消息的所有标记`);
  }, [selectedMessageIndices, processedData?.meta_info?.uuid, markedItems, importantItems, deletedItems, saveStateForUndo, updateMarkingState]);

  const handleClearAllCompletedMarks = useCallback(() => {
    const fileUuid = processedData?.meta_info?.uuid;
    if (!fileUuid || markedItems.size === 0) { 
      setStatusMessage('当前文件没有已完成标记'); 
      return; 
    }
    saveStateForUndo();
    const newState = { 
      markedItems: new Set(), 
      importantItems: new Set(importantItems), 
      deletedItems: new Set(deletedItems) 
    };
    updateMarkingState(newState);
    setStatusMessage(`已清除当前文件的所有已完成标记（${markedItems.size}条）`);
  }, [processedData, markedItems, importantItems, deletedItems, saveStateForUndo, updateMarkingState, setStatusMessage]);

  const isJumpingRef = useRef(false);
  const currentJumpIdRef = useRef(null);
  const isSwitchingFileRef = useRef(false);
  // const lastSearchResultRef = useRef(null); // Already removed

const handleDeleteAllCompletedMarks = useCallback(() => {
  const fileUuid = processedData?.meta_info?.uuid;
  if (!fileUuid || markedItems.size === 0) { 
    setStatusMessage('当前文件没有已完成标记'); 
    return; 
  }
  saveStateForUndo();
  const markedItemsArray = Array.from(markedItems);
  const newMarked = new Set();
  const newImportant = new Set(importantItems);
  const newDeleted = new Set(deletedItems);
  markedItemsArray.forEach(index => {
    newDeleted.add(index);
  });
  const newState = { 
    markedItems: newMarked, 
    importantItems: newImportant, 
    deletedItems: newDeleted 
  };
  updateMarkingState(newState);
  setStatusMessage(`已将${markedItemsArray.length}条已完成标记的消息设为已移除状态`);
}, [processedData, markedItems, importantItems, deletedItems, saveStateForUndo, updateMarkingState, setStatusMessage]);
  
  const updateSelectedMessagesFromBranches = useCallback((branchIndices) => {
    if (!processedData?.branches || branchIndices.length === 0) return;
    const allMessageIndices = new Set();
    branchIndices.forEach(branchIdx => {
      if (branchIdx >= 0 && branchIdx < processedData.branches.length) {
        const branch = processedData.branches[branchIdx];
        if (branch && Array.isArray(branch)) {
          branch.forEach(msg => { 
            if (msg && typeof msg.index === 'number') 
              allMessageIndices.add(msg.index); 
          });
        }
      }
    });
    setSelectedMessageIndices(Array.from(allMessageIndices).sort((a, b) => a - b));
  }, [processedData]);

  const handleSelectMultipleBranches = useCallback((indices) => {
    if (!processedData?.branches) return;
    const ctrlPressed = ctrlKeyPressedRef.current;
    setLastSelectionSource('branch');
    if (ctrlPressed && selectedBranchIndices.length > 0) {
      const newSelection = new Set(selectedBranchIndices);
      indices.forEach(idx => {
        if (newSelection.has(idx)) {
          newSelection.delete(idx);
        } else {
          newSelection.add(idx);
        }
      });
      const finalIndices = Array.from(newSelection).sort((a, b) => a - b);
      setSelectedBranchIndices(finalIndices);
      if (finalIndices.length === 0) {
        setSelectedMessageIndices([]);
        return;
      }
      updateSelectedMessagesFromBranches(finalIndices);
    } else {
      setSelectedBranchIndices(indices);
      if (indices.length === 0) {
        setSelectedMessageIndices([]);
        return;
      }
      updateSelectedMessagesFromBranches(indices);
    }
  }, [processedData, selectedBranchIndices, updateSelectedMessagesFromBranches]); // Added updateSelectedMessagesFromBranches
  
  const jumpToMessage = useCallback((fileIndex, messageIndex) => {
    if (isJumpingRef.current) {
      console.log("跳转操作正在进行中，忽略新请求");
      return;
    }
    isJumpingRef.current = true;
    const jumpId = `jump_${Date.now()}`;
    console.log(`[${jumpId}] 开始跳转到: 文件${fileIndex} 消息${messageIndex}`);
    setSelectedMessageIndices([]);
    setSelectedBranchIndices([]);
    setIsSearching(true);
    const currentJumpId = Date.now();
    currentJumpIdRef.current = currentJumpId;
    const jumpTimeout = setTimeout(() => {
      if (isJumpingRef.current && currentJumpId === currentJumpIdRef.current) {
        console.warn(`[${jumpId}] 跳转到消息超时`);
        isJumpingRef.current = false;
        setIsSearching(false);
      }
    }, 5000); 
    
    const finishJump = () => { 
      if (!isMountedRef.current || currentJumpId !== currentJumpIdRef.current) {
        clearTimeout(jumpTimeout);
        isJumpingRef.current = false;
        return;
      }
      console.log(`[${jumpId}] 执行最终跳转步骤`);
      requestAnimationFrame(() => {
        if (!isMountedRef.current || currentJumpId !== currentJumpIdRef.current) {
          clearTimeout(jumpTimeout);
          isJumpingRef.current = false;
          return;
        }
        const messageExists = processedData?.chat_history?.some(msg => msg.index === messageIndex);
        if (!messageExists) {
          console.warn(`[${jumpId}] 消息索引 ${messageIndex} 在文件 ${fileIndex} 中不存在`);
          setIsSearching(false);
          isJumpingRef.current = false;
          clearTimeout(jumpTimeout);
          return;
        }
        console.log(`[${jumpId}] 设置选中消息: ${messageIndex}`);
        setSelectedMessageIndices([messageIndex]); 
        requestAnimationFrame(() => {
          try {
            if (messageDetailRef.current) {
              console.log(`[${jumpId}] 尝试滚动到消息位置`);
              // Attempt to find the message element within MessageDetail's scroll container
              const scrollContainer = messageDetailRef.current.querySelector('.message-detail-scroll-container');
              if (scrollContainer) {
                  const messageElement = scrollContainer.querySelector(`[data-message-index="${messageIndex}"]`);
                  if (messageElement) {
                      messageElement.scrollIntoView({ behavior: 'smooth', block: 'center' });
                      console.log(`[${jumpId}] 成功滚动到消息位置 (in scroll container)`);
                  } else {
                      // Fallback to searching in the whole messageDetailRef if not found in specific scroll container
                      const fallbackElement = messageDetailRef.current.querySelector(`[data-message-index="${messageIndex}"]`);
                      if (fallbackElement) {
                          fallbackElement.scrollIntoView({ behavior: 'smooth', block: 'center' });
                          console.log(`[${jumpId}] 成功滚动到消息位置 (fallback)`);
                      } else {
                          console.warn(`[${jumpId}] 未找到消息元素 data-message-index="${messageIndex}"`);
                      }
                  }
              } else {
                   console.warn(`[${jumpId}] 未找到 .message-detail-scroll-container`);
                   // Fallback if specific scroll container isn't found
                   const fallbackElement = messageDetailRef.current.querySelector(`[data-message-index="${messageIndex}"]`);
                   if (fallbackElement) {
                       fallbackElement.scrollIntoView({ behavior: 'smooth', block: 'center' });
                       console.log(`[${jumpId}] 成功滚动到消息位置 (messageDetailRef fallback)`);
                   } else {
                        console.warn(`[${jumpId}] 未找到消息元素 data-message-index="${messageIndex}" in messageDetailRef`);
                   }
              }
            } else {
              console.warn(`[${jumpId}] messageDetailRef.current为空`);
            }
            setTimeout(() => {
              if (isMountedRef.current && currentJumpId === currentJumpIdRef.current) {
                console.log(`[${jumpId}] 跳转完成，重置状态`);
                setIsSearching(false);
                isJumpingRef.current = false;
                clearTimeout(jumpTimeout);
              }
            }, 300); 
          } catch (error) {
            console.error(`[${jumpId}] 自动滚动到消息位置时出错:`, error);
            setIsSearching(false);
            isJumpingRef.current = false;
            clearTimeout(jumpTimeout);
          }
        });
      });
    };
    
    if (fileIndex !== currentFileIndex) { 
      try {
        isSwitchingFileRef.current = true;
        console.log(`[${jumpId}] 需要切换文件: ${currentFileIndex} -> ${fileIndex}`);
        setCurrentFileIndex(fileIndex);
        setTimeout(() => {
          if (currentJumpId !== currentJumpIdRef.current || !isMountedRef.current) {
            clearTimeout(jumpTimeout);
            isJumpingRef.current = false;
            isSwitchingFileRef.current = false;
            return;
          }
          console.log(`[${jumpId}] 文件切换完成`);
          isSwitchingFileRef.current = false;
          setTimeout(() => {
            console.log(`[${jumpId}] 文件加载后执行最终跳转`);
            finishJump();
          }, 300); 
        }, 800); 
      } catch (error) {
        console.error(`[${jumpId}] 切换文件时出错:`, error);
        isJumpingRef.current = false;
        isSwitchingFileRef.current = false;
        setIsSearching(false);
        clearTimeout(jumpTimeout);
      }
    } else { 
      console.log(`[${jumpId}] 在当前文件内跳转，直接执行`);
      finishJump(); 
    }
  }, [currentFileIndex, processedData, messageDetailRef, setSelectedMessageIndices, setSelectedBranchIndices, setIsSearching]);


  const handleGlobalSearchPrev = useCallback(() => {
    if (window.searchManager && !isSearching && !isJumpingRef.current && !isSwitchingFileRef.current) {
      setIsSearching(true); 
      window.searchManager.prevResult();
      setTimeout(() => {
        if (isMountedRef.current && isSearching) setIsSearching(false);
      }, 1000);
    } else {
      console.log('搜索或导航操作进行中，或搜索未初始化。');
    }
  }, [isSearching]);

  const handleGlobalSearchNext = useCallback(() => {
    if (window.searchManager && !isSearching && !isJumpingRef.current && !isSwitchingFileRef.current) {
      setIsSearching(true); 
      window.searchManager.nextResult();
      setTimeout(() => {
        if (isMountedRef.current && isSearching) setIsSearching(false);
      }, 1000);
    } else {
      console.log('搜索或导航操作进行中，或搜索未初始化。');
    }
  }, [isSearching]);

const formatStatusMessage = (message) => {
  if (message.includes('文件加载完成:')) {
    const fileName = message.split('文件加载完成:')[1].trim();
    if (fileName.length > 30) {
      return `文件已加载: ${fileName.substring(0, 27)}...`;
    }
  }
  return message;
};

  const performExport = useCallback((dataToExport, baseFileName, exportType, additionalConfig = {}) => {
    // 检查 dataToExport 和 dataToExport.chat_history 是否存在
    if (!dataToExport || !dataToExport.chat_history) {
      setStatusMessage('没有数据可导出');
      return false;
    }

    // 获取当前文件的 deletedItems 状态
    const currentFileDeletedItems = deletedItems;

    // 过滤 chat_history，排除已删除的消息
    // 仅当 exportMarkedOnly 为 false 或未定义时，才执行此过滤
    // 因为如果 exportMarkedOnly 为 true，exportChatAsMarkdown 内部会根据 markedItems 过滤，
    // 我们不希望双重过滤或意外排除仅标记但未删除的消息。
    // 对于“导出当前文件”按钮，additionalConfig.exportMarkedOnly 通常是 false。
    let chatHistoryForExport = dataToExport.chat_history;
    if (!additionalConfig.exportMarkedOnly) {
        chatHistoryForExport = dataToExport.chat_history.filter(
            msg => !currentFileDeletedItems.has(msg.index)
        );
    }


    // 创建用于导出的新数据对象
    const dataForActualExport = {
      ...dataToExport,
      chat_history: chatHistoryForExport,
    };

    const finalExportConfig = {
      ...exportConfig,
      markedItems: new Set([...markedItems, ...importantItems]), // 用于 exportChatAsMarkdown 内部的 exportMarkedOnly 逻辑
      obsidianTags,
      obsidianProperties,
      ...additionalConfig
    };

    try {
      // 将过滤后的数据传递给 exportChatAsMarkdown
      const markdown = exportChatAsMarkdown(dataForActualExport, finalExportConfig);
      const timestamp = new Date().toISOString().slice(0, 10);
      const fileName = `${baseFileName}_${exportType}_${timestamp}.md`;
      saveTextFile(markdown, fileName);
      setStatusMessage(`已导出到: ${fileName}`);
      return true;
    } catch (error) {
      console.error("Export failed:", error);
      setStatusMessage(`导出失败: ${error.message}`);
      return false;
    }
  }, [exportConfig, markedItems, importantItems, obsidianTags, obsidianProperties, deletedItems]); // 将 deletedItems 添加到依赖数组

  const handleExportMarkdown = useCallback(() => performExport(processedData, processedData?.meta_info?.title || 'chat', '完整导出'), [processedData, performExport]);
  
  const handleExportMarked = useCallback(() => {
    const markedCount = markedItems.size + importantItems.size;
    if (markedCount === 0) { setStatusMessage('当前文件没有标记为"已完成"或"重点"的消息'); return; }
    
    // 对于仅导出已标记内容，我们首先获取所有未被删除的标记项
    const exportableMarkedMessages = Array.from(new Set([...markedItems, ...importantItems]))
        .filter(index => !deletedItems.has(index)) // 确保不导出已删除的标记项
        .map(index => processedData.chat_history.find(msg => msg.index === index))
        .filter(Boolean);

    if (exportableMarkedMessages.length === 0) {
        setStatusMessage('所有已标记的消息均已被移除，无法导出');
        return;
    }
    
    const dataForExport = {
        ...processedData,
        chat_history: exportableMarkedMessages
    };

    performExport(dataForExport, processedData?.meta_info?.title || 'chat', '已标记内容', { exportMarkedOnly: true });
  }, [processedData, markedItems, importantItems, deletedItems, performExport]);


  const handleExportSelected = useCallback(() => {
    if (!processedData || selectedMessageIndices.length === 0) { setStatusMessage('未选择消息'); return; }
    const selectedMessages = selectedMessageIndices
        .map(index => processedData.chat_history.find(msg => msg.index === index))
        .filter(Boolean)
        .filter(msg => !deletedItems.has(msg.index)); // 确保不导出已删除的选中消息

    if (selectedMessages.length === 0) {
        setStatusMessage('所有选中的消息均已被移除，无法导出');
        return;
    }
    const customData = { meta_info: { ...processedData.meta_info, title: `${processedData.meta_info.title || '对话'} - 选中${selectedMessages.length}条` }, chat_history: selectedMessages };
    performExport(customData, processedData?.meta_info?.title || 'chat', '选中消息', { exportMarkedOnly: false });
  }, [processedData, selectedMessageIndices, deletedItems, performExport]);

  const handleExportSelectedBranch = useCallback(() => {
    if (selectedBranchIndices.length === 0 || !processedData?.branches) { setStatusMessage('未选择分支'); return; }
    const selectedMessages = []; const messageIndices = new Set();
    selectedBranchIndices.forEach(branchIdx => {
      const branch = processedData.branches[branchIdx];
      if (branch?.length > 0) { 
          branch.forEach(msg => { 
              if (!messageIndices.has(msg.index) && !deletedItems.has(msg.index)) { // 确保不导出已删除的分支消息
                  messageIndices.add(msg.index); 
                  selectedMessages.push(msg); 
              } 
          }); 
      }
    });
    if (selectedMessages.length === 0) { setStatusMessage('选中的分支没有可导出的消息（可能均已被移除）'); return; }
    selectedMessages.sort((a, b) => a.index - b.index);
    const branchLabel = selectedBranchIndices.length === 1 ? `分支${selectedBranchIndices[0] + 1}` : `${selectedBranchIndices.length}个分支`;
    const customData = { meta_info: { ...processedData.meta_info, title: `${processedData.meta_info.title || '对话'} - ${branchLabel}` }, chat_history: selectedMessages };
    performExport(customData, processedData?.meta_info?.title || 'chat', branchLabel, { exportMarkedOnly: false });
  }, [processedData, selectedBranchIndices, deletedItems, performExport]);

  const handleExportAndMark = useCallback(() => {
    const itemsToExportAndMark = new Set([...markedItems, ...importantItems].filter(index => !deletedItems.has(index)));
    
    if (itemsToExportAndMark.size === 0) { 
        setStatusMessage('当前文件没有可导出的"已完成"或"重点"的消息（可能均已被移除）'); 
        return; 
    }

    const exportableMessages = Array.from(itemsToExportAndMark)
        .map(index => processedData.chat_history.find(msg => msg.index === index))
        .filter(Boolean);

    const dataForExport = {
        ...processedData,
        chat_history: exportableMessages
    };

    const exportSuccess = performExport(dataForExport, processedData?.meta_info?.title || 'chat', '已标记内容', { exportMarkedOnly: true });
    
    if (exportSuccess && processedData?.meta_info?.uuid) {
      saveStateForUndo();
      // const fileUuid = processedData.meta_info.uuid; // Removed as unused
      const newDeleted = new Set(deletedItems); 
      itemsToExportAndMark.forEach(idx => newDeleted.add(idx));
      
      const newMarked = new Set(markedItems); 
      const newImportant = new Set(importantItems);
      itemsToExportAndMark.forEach(idx => { newMarked.delete(idx); newImportant.delete(idx); });
      
      const newState = { markedItems: newMarked, importantItems: newImportant, deletedItems: newDeleted };
      updateMarkingState(newState);
      setStatusMessage(`已导出 ${itemsToExportAndMark.size} 条标记消息并将其标记为已删除`);
    }
  }, [processedData, markedItems, importantItems, deletedItems, performExport, saveStateForUndo, updateMarkingState]);

  const handleExportAllMarked = useCallback(async () => {
    if (!files || files.length === 0) { setStatusMessage('没有加载文件'); return; }
    setStatusMessage('正在准备批量导出...');
    let successCount = 0; let markedMessagesCount = 0;
    const timestamp = new Date().toISOString().slice(0, 10); const zip = new JSZip();
    const filesToUpdateState = [];
    try {
      for (let i = 0; i < files.length; i++) {
        if (!isMountedRef.current) return; 
        const file = files[i]; setStatusMessage(`处理文件 ${i + 1}/${files.length}: ${file.name}`);
        try {
          const text = await file.text(); 
          if (!isMountedRef.current) return; 
          const jsonData = JSON.parse(text);
          let data = extractChatData(jsonData); data = detectBranches(data);
          const fileUuid = data.meta_info.uuid; if (!fileUuid) continue;
          
          let fileMarkedSet = new Set(fileMarkedItems.get(fileUuid) || []);
          let fileImportantSet = new Set(fileImportantItems.get(fileUuid) || []);
          let fileDeletedSet = new Set(fileDeletedItems.get(fileUuid) || []);

          // Fallback to localStorage if not in memory (e.g., app just loaded)
          if (fileMarkedSet.size === 0 && fileImportantSet.size === 0 && !fileMarkedItems.has(fileUuid)) {
             try { 
                 const savedState = localStorage.getItem(`chat_marks_${fileUuid}`); 
                 if (savedState) { 
                     const parsed = JSON.parse(savedState); 
                     fileMarkedSet = new Set(parsed.markedItems || []); 
                     fileImportantSet = new Set(parsed.importantItems || []);
                     // Ensure fileDeletedSet is also loaded if not already in memory from a previous load
                     if (!fileDeletedItems.has(fileUuid)) {
                        fileDeletedSet = new Set(parsed.deletedItems || []);
                     }
                 } 
             } catch (e) { console.error("Error loading marks during batch export", e); }
          }
          
          const itemsToExport = new Set([...fileMarkedSet, ...fileImportantSet].filter(index => !fileDeletedSet.has(index))); 
          
          if (itemsToExport.size === 0) continue;

          const exportableMessagesHistory = data.chat_history.filter(msg => itemsToExport.has(msg.index));
          const dataForBatchExport = {
              ...data,
              chat_history: exportableMessagesHistory
          };

          const fileExportConfig = { ...exportConfig, exportMarkedOnly: true, markedItems: itemsToExport, obsidianTags, obsidianProperties };
          const markdown = exportChatAsMarkdown(dataForBatchExport, fileExportConfig);
          const safeFileName = (data.meta_info.title || `chat_${i}`).replace(/[/\\?%*:|"<>]/g, '-');
          zip.file(`${safeFileName}_已标记内容.md`, markdown);
          
          const newDeletedForFile = new Set(fileDeletedSet); 
          itemsToExport.forEach(idx => newDeletedForFile.add(idx));
          const newMarkedForFile = new Set(); 
          const newImportantForFile = new Set();
          
          filesToUpdateState.push({ 
              fileUuid, 
              newState: { 
                  markedItems: newMarkedForFile, 
                  importantItems: newImportantForFile, 
                  deletedItems: newDeletedForFile 
              } 
          });
          successCount++; 
          markedMessagesCount += itemsToExport.size;
        } catch (error) { console.error(`处理文件 ${file.name} 出错:`, error); zip.file(`ERROR_${file.name}.txt`, `处理文件时出错: ${error.message}`); }
      }
      if (!isMountedRef.current) return;
      if (successCount === 0) { setStatusMessage('所有加载的文件中都没有找到可导出的已标记内容'); return; }
      
      setStatusMessage('正在生成ZIP文件...');
      const zipContent = await zip.generateAsync({ type: 'blob' });
      const zipFileName = `Claude对话_批量已标记_${timestamp}.zip`;
      saveTextFile(zipContent, zipFileName);
      
      filesToUpdateState.forEach(({ fileUuid, newState }) => {
          setFileMarkedItems(prev => new Map(prev).set(fileUuid, Array.from(newState.markedItems)));
          setFileImportantItems(prev => new Map(prev).set(fileUuid, Array.from(newState.importantItems)));
          setFileDeletedItems(prev => new Map(prev).set(fileUuid, Array.from(newState.deletedItems)));
          saveMarkState(fileUuid, newState.markedItems, newState.importantItems, newState.deletedItems);
          if (processedData?.meta_info?.uuid === fileUuid) { 
              setMarkedItems(newState.markedItems); 
              setImportantItems(newState.importantItems); 
              setDeletedItems(newState.deletedItems); 
          }
      });
      setStatusMessage(`已成功导出 ${successCount} 个文件的 ${markedMessagesCount} 条标记内容到 ${zipFileName}，并将其标记为已删除`);
    } catch (error) { console.error("批量导出出错:", error); setStatusMessage(`批量导出失败: ${error.message}`); }
     finally { 
       if (isMountedRef.current) {
         setStatusMessage(prev => prev.replace('正在准备批量导出...', '').replace('正在生成ZIP文件...', '')); 
       }
     }
  }, [files, fileMarkedItems, fileImportantItems, fileDeletedItems, exportConfig, obsidianTags, obsidianProperties, saveMarkState, processedData?.meta_info?.uuid]);

  const handleCopySelected = useCallback(() => {
    if (selectedMessageIndices.length === 0 || !processedData) { setStatusMessage('未选择消息'); return; }
    const selectedTexts = selectedMessageIndices
        .map(index => processedData.chat_history.find(msg => msg.index === index))
        .filter(Boolean)
        .filter(msg => !deletedItems.has(msg.index)) // 确保不复制已删除的消息
        .map(msg => `【${msg.sender_label} @ ${msg.timestamp}】\n${msg.display_text}\n---`)
        .join('\n');
    if (!selectedTexts) {
        setStatusMessage('所有选中的消息均已被移除，无法复制');
        return;
    }
    navigator.clipboard.writeText(selectedTexts).then(() => setStatusMessage(`已复制 ${selectedMessageIndices.filter(idx => !deletedItems.has(idx)).length} 条消息到剪贴板`)).catch(err => setStatusMessage(`复制失败: ${err.message}`));
  }, [processedData, selectedMessageIndices, deletedItems]);

  const toggleBottomPanel = useCallback(() => {
    if (showBottomPanel) {
      setShowBottomPanel(false);
    } else {
      setShowBottomPanel(true);
    }
  }, [showBottomPanel]);

  const handleSaveSettings = useCallback((newSettings) => {
    setUserSettings(newSettings); saveSettings(newSettings); setShowSettingsPanel(false); setStatusMessage('设置已保存');
  }, []);

  // --- Render ---
  return (
    <div className="App flex flex-col h-screen bg-gray-50">
      <input type="file" ref={fileInputRef} onChange={handleFileChange} style={{ display: 'none' }} multiple accept=".json" />
      <SearchManager
        searchQuery={searchQuery} setSearchQuery={setSearchQuery} files={files} currentFileIndex={currentFileIndex} processedData={processedData}
        setSelectedMessageIndices={setSelectedMessageIndices} setCurrentFileIndex={setCurrentFileIndex} setIsSearching={setIsSearching} setShowSearchPanel={setShowSearchPanel}
      />
      {showSearchPanel && ( 
        <SearchPanel 
          isOpen={showSearchPanel} 
          onClose={() => setShowSearchPanel(false)} 
          files={files} 
          currentFileIndex={currentFileIndex} 
          processedData={processedData} 
          searchQuery={searchQuery} 
          setSearchQuery={setSearchQuery} 
          jumpToMessage={jumpToMessage} 
          handleSelectFile={setCurrentFileIndex}
        /> 
      )}
      {showSettingsPanel && ( 
        <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-50"> 
          <UserSettingsPanel currentSettings={userSettings} onSave={handleSaveSettings} onCancel={() => setShowSettingsPanel(false)} /> 
        </div> 
      )}

      {files.length === 0 ? (
        <WelcomePage handleLoadClick={handleLoadClick} />
      ) : (
        <div className="flex flex-1 overflow-hidden">
          <div className="flex flex-col border-r border-gray-200 w-2/5 min-w-[400px] max-w-[600px] bg-white shadow-sm">
            <div className="p-3 bg-gray-50 border-b border-gray-200 flex-shrink-0">
              <h2 className="font-semibold text-base text-center text-gray-800 truncate" title={processedData?.meta_info?.title || files[currentFileIndex]?.name}> {processedData?.meta_info?.title || files[currentFileIndex]?.name || '无标题对话'} </h2>
              <div className="flex justify-between items-center text-xs text-gray-500 mt-1">
                <div className="flex items-center space-x-2"> <span> {processedData?.chat_history?.length || 0}条消息 {processedData?.branches?.length > 0 && ` | ${processedData.branches.length}个分支`} </span> <button onClick={handleLoadClick} title="添加文件" className="text-blue-500 hover:text-blue-700"><PlusCircle size={14} /></button> <button onClick={handleRemoveCurrentFile} title="移除当前文件" className="text-red-500 hover:text-red-700"><Trash2 size={14} /></button> </div>
                {files.length > 1 && ( <div className="flex items-center space-x-1"> <button onClick={handlePrevFile} disabled={currentFileIndex === 0} className="p-0.5 border rounded disabled:opacity-50"><ChevronLeft size={14} /></button> <span>{currentFileIndex + 1}/{files.length}</span> <button onClick={handleNextFile} disabled={currentFileIndex === files.length - 1} className="p-0.5 border rounded disabled:opacity-50"><ChevronRight size={14} /></button> </div> )}
              </div>
            </div>
            {processedData && processedData.branches && processedData.branches.length > 0 && (
              <div className="border-b border-gray-200 flex-shrink-0">
                <BranchManager
                  processedData={processedData}
                  selectedBranchIndices={selectedBranchIndices}
                  handleSelectBranch={handleSelectMultipleBranches}
                  handleMarkBranchCompleted={handleMarkDeleted} 
                  handleExportSelectedBranch={handleExportSelectedBranch}
                  handleClearMarks={handleClearMarks} 
                />
              </div>
            )}
            <div className="flex-1 overflow-hidden"> <MessageList messages={processedData?.chat_history || []} selectedIndices={selectedMessageIndices} onMessageSelect={handleMessageSelect} markedItems={markedItems} importantItems={importantItems} deletedItems={deletedItems} searchQuery={searchQuery} setSearchQuery={setSearchQuery} showCompleted={showCompleted} setShowCompleted={setShowCompleted} showDeleted={showDeleted} setShowDeleted={setShowDeleted} handleMarkDeleted={handleMarkDeleted} isSearching={isSearching} showSearchPanel={() => setShowSearchPanel(true)} /> </div>
          </div>

          <div className="flex-1 flex flex-col overflow-hidden relative" ref={rightPanelRef}>
             {searchQuery && searchNavInfo.hasLocalResults && ( // Changed to hasLocalResults
               <div className="absolute top-2 right-3 z-20 flex items-center space-x-1.5 bg-white/80 backdrop-blur-sm p-1.5 rounded-md border border-gray-200 shadow">
                 <button 
                   onClick={handleGlobalSearchPrev} 
                   className="p-1 bg-gray-100 hover:bg-gray-200 rounded text-gray-700" 
                   title="上一个匹配 (当前文件)"
                   disabled={isSearching || !searchNavInfo.hasLocalResults} // Changed to hasLocalResults
                 > 
                   <ChevronUp size={16} /> 
                 </button>
                 <span className="text-xs text-gray-600 px-1">
                   {searchNavInfo.localDisplayIndex > 0 ? `${searchNavInfo.localDisplayIndex}/${searchNavInfo.localTotalCount}` : '0/0'}
                 </span>
                 <button 
                   onClick={handleGlobalSearchNext} 
                   className="p-1 bg-gray-100 hover:bg-gray-200 rounded text-gray-700" 
                   title="下一个匹配 (当前文件)"
                   disabled={isSearching || !searchNavInfo.hasLocalResults} // Changed to hasLocalResults
                 > 
                   <ChevronDown size={16} /> 
                 </button>
                 <button
                   onClick={() => setShowSearchPanel(true)}
                   className="p-1 bg-gray-100 hover:bg-gray-200 rounded text-gray-700"
                   title="打开全局搜索面板"
                 >
                   <Search size={16} />
                 </button>
                 <button
                   onClick={handleClearSearch}
                   className="p-1 bg-gray-100 hover:bg-gray-200 rounded text-gray-700 ml-1"
                   title="清除搜索"
                 >
                   <X size={16} />
                 </button>
               </div>
             )}
            <div className="flex-1 overflow-hidden relative pt-10"> 
              <div className="absolute inset-0 top-0 bottom-0 overflow-y-auto"> 
                 <MessageDetail ref={messageDetailRef} processedData={processedData} selectedMessageIndices={selectedMessageIndices} activeTab={activeTab} setActiveTab={setActiveTab} searchQuery={searchQuery} statusMessage={statusMessage} />
              </div>
            </div>
            <div className="flex-shrink-0 border-t border-gray-300 bg-white" style={{ height: showBottomPanel ? '330px' : '36px' }}>
              <div className={`bg-gray-50 border-b border-gray-200 flex justify-between items-center h-[36px] px-2 ${showBottomPanel ? '' : 'cursor-pointer'}`} onClick={!showBottomPanel ? toggleBottomPanel : undefined}>
                 <div className={`flex ${showBottomPanel ? 'opacity-100' : 'opacity-0 pointer-events-none'}`}> <button onClick={() => setActiveBottomPanelTab('branches')} className={`px-3 py-1 text-xs rounded-t-md ${activeBottomPanelTab === 'branches' ? 'bg-white text-blue-600 border border-gray-200 border-b-white -mb-px' : 'text-gray-500 hover:text-gray-700'}`}>分支统计</button> <button onClick={() => setActiveBottomPanelTab('metadata')} className={`px-3 py-1 text-xs rounded-t-md ${activeBottomPanelTab === 'metadata' ? 'bg-white text-blue-600 border border-gray-200 border-b-white -mb-px' : 'text-gray-500 hover:text-gray-700'}`}>元数据</button> <button onClick={() => setActiveBottomPanelTab('export')} className={`px-3 py-1 text-xs rounded-t-md ${activeBottomPanelTab === 'export' ? 'bg-white text-blue-600 border border-gray-200 border-b-white -mb-px' : 'text-gray-500 hover:text-gray-700'}`}>导出/标记</button> </div>
                 <div className="flex items-center space-x-2">
                    <button onClick={(e) => { if (!showBottomPanel) { e.stopPropagation(); } handleCopySelected(); }} disabled={selectedMessageIndices.length === 0} title="复制选中内容" className="p-1 text-gray-600 hover:text-blue-600 disabled:opacity-50"><Clipboard size={14} /></button>
                    <button onClick={(e) => { if (!showBottomPanel) { e.stopPropagation(); } handleExportSelected(); }} disabled={selectedMessageIndices.length === 0} title="导出选中消息" className="p-1 text-gray-600 hover:text-blue-600 disabled:opacity-50"><Download size={14} /></button>
                    <button onClick={(e) => { if (!showBottomPanel) { e.stopPropagation(); } handleExportMarkdown(); }} title="导出当前文件" className="p-1 text-gray-600 hover:text-blue-600"><FileText size={14} /></button>
                    <button onClick={(e) => { if (!showBottomPanel) { e.stopPropagation(); } handleMarkCompleted(); }} disabled={selectedMessageIndices.length === 0} title="标记为已完成" className="p-1 text-gray-600 hover:text-blue-600 disabled:opacity-50"><CheckSquare size={14} /></button>
                    <button onClick={(e) => { if (!showBottomPanel) { e.stopPropagation(); } setShowSettingsPanel(true); }} title="用户设置" className="flex items-center text-gray-600 hover:text-blue-600"> <Settings size={14} /></button>
                    <button onClick={toggleBottomPanel} title={showBottomPanel ? "收起功能区" : "展开功能区"} className="p-1 text-gray-600 hover:text-blue-600"> {showBottomPanel ? <ChevronDown size={16} /> : <ChevronUp size={16} />} </button>
                  </div>
              </div>
              {showBottomPanel && ( 
                <div className="h-[294px] overflow-auto bg-white"> 
                  {activeBottomPanelTab === 'branches' && 
                    <BranchStatsPanel 
                      processedData={processedData} 
                      selectedMessageIndices={selectedMessageIndices} 
                    />
                  }
                  {activeBottomPanelTab === 'metadata' && 
                    <MetadataPanel 
                      obsidianTags={obsidianTags} 
                      setObsidianTags={setObsidianTags} 
                      obsidianProperties={obsidianProperties} 
                      setObsidianProperties={setObsidianProperties} 
                      processedData={processedData} 
                    /> 
                  } 
                  {activeBottomPanelTab === 'export' && 
                    <ExportPanel 
                      exportConfig={exportConfig} 
                      setExportConfig={setExportConfig} 
                      handleExportMarkdown={handleExportMarkdown} 
                      handleExportAndMark={handleExportAndMark} 
                      handleExportMarked={handleExportMarked} 
                      handleExportAllMarked={handleExportAllMarked} 
                      handleExportSelected={handleExportSelected} 
                      showCompleted={showCompleted} 
                      setShowCompleted={setShowCompleted} 
                      showDeleted={showDeleted} 
                      setShowDeleted={setShowDeleted} 
                      handleMarkCompleted={handleMarkCompleted} 
                      handleMarkImportant={handleMarkImportant} 
                      handleMarkDeleted={handleMarkDeleted} 
                      handleClearMarks={handleClearMarks} 
                      handleCopySelected={handleCopySelected} 
                      handleUndo={handleUndo} 
                      handleRedo={handleRedo} 
                      undoStack={undoStack} 
                      redoStack={redoStack} 
                      selectedMessageIndices={selectedMessageIndices} 
                      markedItems={markedItems} 
                      handleClearAllCompletedMarks={handleClearAllCompletedMarks} 
                      handleDeleteAllCompletedMarks={handleDeleteAllCompletedMarks}
                    /> 
                  } 
                </div> 
              )}
            </div>
          </div>
        </div>
      )}
    </div>
  );
};

export default App;
