// MessageList.js 修复版 - 解决搜索和选择状态冲突

import React, { useState, useCallback, useEffect, useRef } from 'react';
import { Search, Trash2, Loader, X } from 'lucide-react';

const MessageList = ({ 
  messages, 
  selectedIndices, 
  onMessageSelect, 
  markedItems,
  importantItems,
  deletedItems,
  searchQuery,
  setSearchQuery,
  showCompleted,
  setShowCompleted,
  showDeleted,
  setShowDeleted,
  handleMarkDeleted,
  isSearching,
  showSearchPanel
}) => {
  // 使用ref追踪组件挂载状态
  const isMountedRef = useRef(true);
  // 用于跟踪当前高亮的消息
  const highlightedMessagesRef = useRef(new Set());
  // 用于防止快速点击
  const clickTimerRef = useRef(null);
  // 用于延迟更新高亮
  const highlightTimerRef = useRef(null);
  // 用于跟踪选中状态
  const selectedIndicesRef = useRef([]);
  // 用于跟踪Ctrl键状态
  const ctrlKeyPressedRef = useRef(false);
  
  // 同步选中状态到ref
  useEffect(() => {
    selectedIndicesRef.current = selectedIndices;
  }, [selectedIndices]);
  
  // 监听Ctrl键
  useEffect(() => {
    const handleKeyDown = (e) => {
      if (e.ctrlKey || e.metaKey) {
        ctrlKeyPressedRef.current = true;
      }
    };
    
    const handleKeyUp = (e) => {
      if (!e.ctrlKey && !e.metaKey) {
        ctrlKeyPressedRef.current = false;
      }
    };
    
    window.addEventListener('keydown', handleKeyDown);
    window.addEventListener('keyup', handleKeyUp);
    
    return () => {
      window.removeEventListener('keydown', handleKeyDown);
      window.removeEventListener('keyup', handleKeyUp);
    };
  }, []);
  
  // 组件挂载/卸载时设置状态
  useEffect(() => {
    isMountedRef.current = true;
    return () => {
      isMountedRef.current = false;
      if (clickTimerRef.current) clearTimeout(clickTimerRef.current);
      if (highlightTimerRef.current) clearTimeout(highlightTimerRef.current);
    };
  }, []);
  
  // 监听搜索状态更新事件
  useEffect(() => {
    const handleSearchUpdate = (event) => {
      if (!isMountedRef.current) return;
      
      const { totalCount, activeIndex, query, clearSearch } = event.detail;
      
      // 处理清除搜索
      if (clearSearch) {
        highlightedMessagesRef.current.clear();
        if (typeof setSearchQuery === 'function') {
          setSearchQuery('');
        }
        return;
      }
      
      // 确保只处理匹配当前查询的更新
      if (query && query === searchQuery) {
        // 更新高亮状态
        if (highlightTimerRef.current) clearTimeout(highlightTimerRef.current);
        
        highlightTimerRef.current = setTimeout(() => {
          if (!isMountedRef.current) return;
          
          // 强制组件重新渲染以显示最新高亮
          if (typeof setSearchQuery === 'function') {
            setSearchQuery(prev => prev);
          }
        }, 50);
      }
    };
    
    window.addEventListener('search-status-update', handleSearchUpdate);
    return () => window.removeEventListener('search-status-update', handleSearchUpdate);
  }, [searchQuery, setSearchQuery]);
  
  // 搜索相关操作
  const handleSearchSubmit = useCallback((e) => {
    if (e) e.preventDefault();
    
    const query = searchQuery.trim();
    if (!query) return;
    
    // 直接调用 SearchManager
    if (window.searchManager && typeof window.searchManager.search === 'function') {
      window.searchManager.search(query);
    }
  }, [searchQuery]);
  
  // 处理搜索输入框按回车
  const handleSearchKeyPress = useCallback((e) => {
    if (e.key === 'Enter') {
      e.preventDefault();
      handleSearchSubmit();
    }
  }, [handleSearchSubmit]);
  
  // 改进: 清除搜索 - 不会影响选择状态
  const handleClearSearch = useCallback(() => {
    if (!isMountedRef.current) return;
    
    // 清除搜索框内容
    if (typeof setSearchQuery === 'function') {
      setSearchQuery('');
    }
    
    // 通知SearchManager清除搜索
    if (window.searchManager && typeof window.searchManager.clearSearch === 'function') {
      window.searchManager.clearSearch();
    }
    
    // 清除本地高亮状态
    highlightedMessagesRef.current.clear();
    
    // 不再清除选择状态
  }, [setSearchQuery]);
  
  // 改进: 处理消息点击 - 支持Ctrl键多选并保持搜索状态
  const handleMessageClick = useCallback((index, ctrlKey) => {
    // 使用传入的Ctrl键状态或全局监听的状态
    const ctrlPressed = ctrlKey || ctrlKeyPressedRef.current;
    
    // 防止快速多次点击
    if (clickTimerRef.current) {
      clearTimeout(clickTimerRef.current);
    }
    
    // 检查当前是否已选中此消息，以避免不必要的状态更新
    const isCurrentlySelected = 
      selectedIndicesRef.current.length === 1 && 
      selectedIndicesRef.current[0] === index && 
      !ctrlPressed;
    
    if (isCurrentlySelected) {
      // 已经选中，不需要处理
      return;
    }
    
    clickTimerRef.current = setTimeout(() => {
      if (isMountedRef.current) {
        // 清除计时器引用
        clickTimerRef.current = null;
        
        // 调用选择回调
        onMessageSelect(index, ctrlPressed);
      }
    }, 10);
  }, [onMessageSelect]);

  // 更新高亮状态跟踪 - 当searchQuery变化时更新
  useEffect(() => {
    // 当搜索查询为空时清除高亮
    if (!searchQuery.trim()) {
      highlightedMessagesRef.current.clear();
      return;
    }
    
    // 搜索查询非空时，计算应该高亮的消息
    const newHighlighted = new Set();
    const query = searchQuery.toLowerCase();
    
    messages?.forEach(msg => {
      if (msg.display_text && msg.display_text.toLowerCase().includes(query)) {
        newHighlighted.add(msg.index);
      }
    });
    
    // 更新高亮集合
    highlightedMessagesRef.current = newHighlighted;
  }, [searchQuery, messages]);

  return (
    <div className="flex flex-col h-full overflow-hidden">
      {/* 顶部搜索区域 */}
      <div className="p-2 border-b border-gray-300 bg-white">
        <div className="flex space-x-2 items-center">
          <div className="relative flex-1">
            <input 
              id="global-search-input"
              type="text" 
              placeholder="搜索全部消息..." 
              className="w-full pl-8 pr-8 py-1 border border-gray-300 rounded text-sm focus:ring-1 focus:ring-blue-400 focus:border-blue-400"
              value={searchQuery}
              onChange={(e) => setSearchQuery(e.target.value)}
              onKeyPress={handleSearchKeyPress}
            />
            <Search
              className="absolute left-2 top-1.5 h-4 w-4 text-gray-400 cursor-pointer" 
              onClick={handleSearchSubmit}
              title="搜索消息"
            />
            {searchQuery && (
              <button
                className="absolute right-2 top-1.5 text-gray-400 hover:text-gray-600 focus:outline-none"
                onClick={handleClearSearch}
                title="清除搜索"
              >
                <X className="h-4 w-4" />
              </button>
            )}
            {isSearching && (
              <Loader className="absolute right-2 top-1.5 h-4 w-4 text-blue-500 animate-spin" />
            )}
          </div>
          
          <button 
            className="bg-blue-500 text-white py-1 px-2 rounded text-sm whitespace-nowrap hover:bg-blue-600 flex items-center focus:outline-none focus:ring-2 focus:ring-blue-300 focus:ring-offset-1"
            onClick={showSearchPanel}
            title="打开全局搜索面板"
          >
            <Search className="h-3.5 w-3.5 mr-1" />
            全部
          </button>
        </div>
      </div>
      
      {/* 消息列表表头 */}
      <div className="bg-gray-100 border-b border-gray-300 p-1 sticky top-0 z-10 flex-shrink-0">
        <div className="grid grid-cols-12 text-xs font-medium text-gray-600">
          <div className="col-span-1 px-1 text-center">#</div>
          <div className="col-span-2 px-1">发送者</div>
          <div className="col-span-3 px-1">时间</div>
          <div className="col-span-1 px-1 text-center">分支</div>
          <div className="col-span-1 px-1 text-center">标记</div>
          <div className="col-span-3 px-1 truncate">预览</div>
          <div className="col-span-1 px-1 flex justify-end pr-1">
            <button
              className="text-gray-400 hover:text-red-600 focus:outline-none disabled:opacity-50"
              onClick={handleMarkDeleted}
              disabled={selectedIndices.length === 0}
              title="移除选中消息"
            >
              <Trash2 className="h-3.5 w-3.5" />
            </button>
          </div>
        </div>
      </div>
      
      {/* 消息列表 - 可滚动区域 */}
      <div className="flex-1 overflow-y-auto border-gray-200">
        {messages?.map(msg => {
          // 如果消息被标记为已删除且设置为隐藏已删除，则跳过
          if (deletedItems.has(msg.index) && !showDeleted) {
            return null;
          }
          
          // 如果消息被标记为已完成且设置为隐藏已完成，则跳过
          if (markedItems.has(msg.index) && !showCompleted) {
            return null;
          }
          
          // 分支信息
          let branchInfo = "";
          if (msg.is_branch_point) {
            branchInfo = "🔀";
          } else if (msg.branch_level > 0) {
            branchInfo = `↳${msg.branch_level}`;
          }
          
          // 标记信息
          let markInfo = null;
          
          if (deletedItems.has(msg.index)) {
            markInfo = <span className="deleted-mark" style={{color: selectedIndices.includes(msg.index) ? '#ffcdd2' : '#f44336'}}>🗑️</span>;
          } else if (markedItems.has(msg.index) && importantItems.has(msg.index)) {
            markInfo = (
              <div className="dual-mark" style={{color: selectedIndices.includes(msg.index) ? 'white' : 'inherit'}}>
                <span className="completed-mark" style={{color: selectedIndices.includes(msg.index) ? '#a5d6a7' : '#4caf50'}}>✓</span>
                <span className="important-mark" style={{color: selectedIndices.includes(msg.index) ? '#ffe082' : '#ff8f00'}}>⭐</span>
              </div>
            );
          } else if (markedItems.has(msg.index)) {
            markInfo = <span className="completed-mark" style={{color: selectedIndices.includes(msg.index) ? '#a5d6a7' : '#4caf50'}}>✓</span>;
          } else if (importantItems.has(msg.index)) {
            markInfo = <span className="important-mark" style={{color: selectedIndices.includes(msg.index) ? '#ffe082' : '#ff8f00'}}>⭐</span>;
          }

          // 已删除消息的样式
          const isDeleted = deletedItems.has(msg.index);
          const deletedStyle = isDeleted ? { textDecoration: 'line-through', opacity: 0.6 } : {};

          // 确定是否应该高亮显示
          const isHighlighted = highlightedMessagesRef.current.has(msg.index) && searchQuery;
          
          // 每个消息项添加key以避免React渲染问题
          return (
            <div 
              key={`msg-${msg.index}`} 
              className={`border-b border-gray-200 hover:bg-gray-100 ${
                selectedIndices.includes(msg.index) ? 'bg-indigo-100' : (isHighlighted ? 'bg-yellow-50' : '')
              } message-list-item cursor-pointer transition-colors duration-100`}
              onClick={(e) => handleMessageClick(msg.index, e.ctrlKey)}
              style={{
                ...deletedStyle
              }}
              data-message-index={msg.index}
            >
              <div className="grid grid-cols-12 px-1 py-1.5 text-xs items-center">
                <div className="col-span-1 px-1 text-center text-gray-500">{msg.index + 1}</div>
                <div className="col-span-2 px-1 font-medium text-gray-800">{msg.sender_label}</div>
                <div className="col-span-3 px-1 text-gray-500">{msg.timestamp}</div>
                <div className="col-span-1 px-1 text-center text-purple-600">{branchInfo}</div>
                <div className="col-span-1 px-1 text-center mark-container">{markInfo}</div>
                <div className={`col-span-4 px-1 truncate ${
                  selectedIndices.includes(msg.index) ? 'text-indigo-800' : 'text-gray-600'
                }`}>
                  {msg.display_text?.substring(0, 100)}
                </div>
              </div>
            </div>
          );
        })}
        {messages?.length === 0 && (
            <div className="text-center text-gray-500 py-4 text-sm">没有消息</div>
        )}
      </div>
    </div>
  );
};

export default MessageList;